import QNoteElem from './QNote';
import QScore, {chineseNumbers} from './QScore';

	/*
	生成音程题目
	config = {clef: 'treble or bass', }
	谱号： treble(默认)， bass
	度数： 1，2，3，4，5，6，7度
	音数： 1 ～ 12， 纯，大，小，减，增
	14个自然音程（8度以内）：
			   音数
		纯一度： 0  	C-C ...
	  增一度：	1   C-#C
	  倍增一度： 2   C-##C
	  
	  减二度：   0   C-bbD
		小二度： 1	C-bD E-F B-C
		大二度： 2	C-D  D-E F-G G-A A-B
	  增二度：   3   C-#D
	  倍增二度： 4   C-##D

	  倍减三度： 1   bC-bbE
	  减三度：   2   C-bbE
		小三度： 3	C-bE D-F  E-G A-C B-D minor third
		大三度： 4	C-E  F-A G-bE         major third
	  增三度：   5   C-#E
	  倍增三度： 6   C-##E

	  倍减四度： 3   C-bbF
	  减四度：   4   C-bF
		纯四度： 5	C-F  D-G E-A G-C A-D B-E  perfect forth
		增四度： 6	C-#F F-B				Augmented fourth
	  倍增四度： 7   C-##F

	  倍减五度： 5   C-bbG
		减五度： 6	C-bG B-D				Diminished fifth
		纯五度： 7	C-G  D-A E-B F-C G-D A-E perfect fifth
	  增五度：   8   C-#G
	  倍增五度： 9   C-##G

	  倍减六度： 6   bC-bbA
	  减六度：   7   C-bbA
		小六度： 8	C-bA G-bE
		大六度： 9	C-A  G-E
	  增六度：   10   C-#A
	  倍增六度： 11   C-##A

	  倍减七度： 8   bC-bbB
	  减七度：   9   C-bbB
		小七度： 10	C-bB D-C E-D G-F A-G B-A
		大七度： 11	C-B  F-E
	  增七度：   12   C-#B
	  倍增七度： 13   C-##B

	  倍减八度： 10   C-bbC
	  减八度：   11   C-bC
		纯八度： 12	C-C ...
	  增八度：   13   C-#C
	  倍增八度： 14   C-##C

	变化音程：
		可以降低或者增加低音或者冠音。
		增音程：纯音程或者大音程增加半音
		减音程：纯音程或者小音程缩小半音
		倍增音程：增音程增加半音
		倍减音程：减音程缩小半音
	
	单音程：不超过8度的音程：1～7度，倍减八度，减八度和纯八度
	复音程：超过8度的音程：就是相应的单音程加7度。增八度是复音程
	纯1 大2 大3  纯4  纯5  大6  大7  纯8
	纯8 大9 大10 纯11 纯12 大13 大14 纯15
	
	音程的转位：将原音程的底音或冠音做八度移动，使底音和冠音互换位置
		如 大3: G-B -> 小6: B-G
	原位音程 1度 2 3 4 5 6 7 8 增8 9  10 11 12 13 14 15
	转位音程 8度 7 6 5 4 3 2 1 减8 7  6  5  4  3  2  1
	度数之和 9度 9 9 9 9 9 9 9 16  16 16 16 16 16 16 16

	音数变化           0  1 
	原位音程 倍减 减 小 纯 大 增 倍增
	转位音程 倍增 增 大 纯 小 减 倍减

	等音程：音响相同，记法和意义不同
		度数相等的等音程：将两个音同时升高#或降低b
		度数不相等的等音程：大3=减4， 纯5=倍增4， 小6=增5
	音程协和度：
		纯八度：非常协和
		纯四度与纯五度：协和
		三度与六度：比较协和
		二度音程与增四减五：不协和
		小二度：非常不协和
		变化音程先转成自然音程在判断是否协和。
	练习：
		给一个五线谱，要求判断音程名称，是自然音程还是变化音程。
		给一个冠音/低音，要求向下/上构成一个大三度/减七度/增十度音程。
		下面哪个音程是不存在的。
		判断音程是否协和
	*/
const intervalInfo = {
			//倍减 减 小 纯 大 增 倍增
			'纯一度':   {step: 0, yinshu: 0},
			'增一度':   {step: 0, yinshu: 1},
			'倍增一度': {step: 0, yinshu: 2},

			'减二度':   {step: 1, yinshu: 0},
			'小二度':   {step: 1, yinshu: 1},
			'大二度':   {step: 1, yinshu: 2},
			'增二度':   {step: 1, yinshu: 3},
			'倍增二度': {step: 1, yinshu: 4},

			'倍减三度': {step: 2, yinshu: 1},
			'减三度':   {step: 2, yinshu: 2},
			'小三度':   {step: 2, yinshu: 3},
			'大三度':   {step: 2, yinshu: 4},
			'增三度':   {step: 2, yinshu: 5},
			'倍增三度': {step: 2, yinshu: 6},
			
			'倍减四度': {step: 3, yinshu: 3},
			'减四度':   {step: 3, yinshu: 4},
			'纯四度':   {step: 3, yinshu: 5},
			'增四度':   {step: 3, yinshu: 6},
			'倍增四度': {step: 3, yinshu: 7},

			//练耳时'增四度','减五度' 合并为三全音：Tritone
			'三全音':   {step: 3, yinshu: 6},

			'倍减五度': {step: 4, yinshu: 5},
			'减五度':   {step: 4, yinshu: 6},
			'纯五度':   {step: 4, yinshu: 7},
			'增五度':   {step: 4, yinshu: 8},
			'倍增五度': {step: 4, yinshu: 9},

			'倍减六度': {step: 5, yinshu: 6},
			'减六度':   {step: 5, yinshu: 7},
			'小六度':   {step: 5, yinshu: 8},
			'大六度':   {step: 5, yinshu: 9},
			'增六度':   {step: 5, yinshu: 10},
			'倍增六度': {step: 5, yinshu: 11},

			'倍减七度': {step: 6, yinshu: 8},
			'减七度':   {step: 6, yinshu: 9},
			'小七度':   {step: 6, yinshu: 10},
			'大七度':   {step: 6, yinshu: 11},
			'增七度':   {step: 6, yinshu: 12},
			'倍增七度': {step: 6, yinshu: 13},

			'倍减八度': {step: 7, yinshu: 10},
			'减八度':   {step: 7, yinshu: 11},
			'纯八度':   {step: 7, yinshu: 12},
			'增八度':   {step: 7, yinshu: 13},
			'倍增八度': {step: 7, yinshu: 14},
			//大于8度的音程： ?(n)度=纯8度 + ?(n-7)度 如： 大9度=纯8度+大2度
			'小九度':   {step: 8, yinshu: 13}, //纯八度+小二度
			'大九度':   {step: 8, yinshu: 14}, //纯八度+大二度
			'纯十一度': {step: 10, yinshu: 17}, //纯八度+纯四度
			'大十三度': {step: 12, yinshu: 21}, //纯八度+大六度
		}
const intervals = [
		//倍减 减 小 纯 大 增 倍增
		{types:['纯','增','倍增'], step:0, yinshu:0},
		{types:['减','小','大','增','倍增'], step:1, yinshu:0},
		{types:['倍减','减','小','大','增','倍增'], step:2, yinshu:1},
		{types:['倍减','减','纯','增','倍增'], step:3, yinshu:3},
		{types:['倍减','减','纯','增','倍增'], step:4, yinshu:5},
		{types:['倍减','减','小','大','增','倍增'], step:5, yinshu:6},
		{types:['倍减','减','小','大','增','倍增'], step:6, yinshu:8},
		{types:['倍减','减','纯','增','倍增'], step:7, yinshu:10},
		]
const intervalTypes = ['倍减','减','小','纯','大','增','倍增']

class QInterval extends QScore {
	/*
	config={
		types: ['减','小','纯','大','增']
		steps: [3,7,9,11,13]
	}
	*/
	static randomIntervalNames(config, count, except) {
		var res = []
		let interval = intervalInfo[except]
		if (!interval) {
			console.log('unknow interval:'+except)
		}
		var exceptStep = intervalInfo[except].step
		for (var i = 0; i < 10; i++) {
			var deltaStep = 0
			if (res.length<1) {
				deltaStep = exceptStep
			}else if (res.length<2) {
				deltaStep = exceptStep+1
			}else{
				deltaStep = exceptStep>0 ? exceptStep-1 : exceptStep + 1
			}
			var type
			if (config && config.types && config.types.length>0) {
				type = config.types[QNoteElem.random(config.types.length-1)]
			}else{
				type = intervalTypes[QNoteElem.random(intervalTypes.length-1)]
			}
			var name = type+chineseNumbers[deltaStep]+'度'

			if (intervalInfo[name] && name !== except && res.indexOf(name)<0) {
				res.push(name)
				if (res.length>=count) {
					break
				}
			}
		}
		return res
	}
	

	/*
	params:
		rootNote: 根音
		intervalType： {type: '倍减'、'减'、'小'、'纯'、'大'、'增'、'倍增', step: 0...7}
		alter: null 随机， -2， -1， 0， 1， 2
	return:
		{clef: string, name:String, head:{step: Int, alter: Int, octave: Int, line: Int}}
	*/
	intervalFromRoot(rootNote, intervalType) {
		var yinshu = -1
		var step = 0
		var name = ''
		var type = ''
		if (intervalType.type) {
			step = intervalType.step
			type = intervalType.type
			var interval = intervals[intervalType.step<8 ? intervalType.step : intervalType.step-7]
			var typeIndex = interval.types.indexOf(intervalType.type)
			if (typeIndex<0) {
				interval = null
			}else{
				yinshu = interval.yinshu+typeIndex
				if (step>7) {
					yinshu += 12
				}
			}
			name = intervalType.type+chineseNumbers[step]+'度'
			if (!intervalInfo[name]) {
				return
			}
		}else{
			var info = intervalInfo[intervalType]
			yinshu = info.yinshu
			step = info.step
			name = intervalType
			type = intervalType.charAt(0)
		}
		if (yinshu>=0) {
			var headNote = rootNote.add(step, yinshu)
			if (headNote.alter<-2 || headNote.alter>2) {
				// console.log('error alter headNote')
				// console.log(rootNote)
				// console.log(headNote)
			}else{
				// if (Number.isNaN(intervalType.alter)) {
				// 	intervalType.alter = 0
				// }
				if ((intervalType.alter === 0 && headNote.alter===0) ||
					(intervalType.alter && headNote.alter===intervalType.alter) ||
					(!intervalType.alter && intervalType.alter !== 0)
					) {
					rootNote.setNoteValueLineAcc('treble')
					headNote.setNoteValueLineAcc('treble')
					if (headNote.line === rootNote.line && rootNote.alter !== headNote.alter) {
						if (rootNote.alter===0) {
							rootNote.accidental = 'natural'
						}else if (headNote.alter===0) {
							headNote.accidental = 'natural'
						}
					}
					// console.log(rootNote)
					return {name: name, root:rootNote, head: headNote, step: step, type: type}
				}else{
					console.log('error alter headNote')
					console.log(intervalType)
					console.log(rootNote)
					console.log(headNote)
				}

			}
		}
	}
	/*
	3个难度：
	难度1是自然音程
	难度2带有增减的变化音程
	难度3是带有倍增和倍减的变化音程
	
	params:
		config: null为随机音程
		{
			clefs: null 随机谱号，或['bass', 'treble','C']
			steps: null 随机度数, 或[1,2,3,4,5,6,7,8] 度音程, 
			types: null 随机类型，或['倍减','减','小','纯','大','增','倍增']
			intervalNames: ['纯一度','小二度','大二度','小三度','大三度','纯四度',...]
		}
	return:
		{
			name: 音程名字
			type: '倍减','减','小','纯','大','增','倍增'
			step: 1...8
			root: {step, octave, alter, line, value}
			head: {step, octave, alter, line, value}
		}

	*/

	static randomInterval(config) {
		var res = new QInterval()

		while(true){
			let alter
			if (config && config.alters && config.alters.length>0) {
				alter = config.alters[QNoteElem.random(config.alters.length-1)]
			}
			let rootNote
			if (config.rootNoteRange && config.rootNoteRange.length>1) {
				rootNote = QNoteElem.randomNoteWithConfig({alter: alter, min: config.rootNoteRange[0], max: config.rootNoteRange[1]})
			}else{
				rootNote = QNoteElem.randomNote(alter, config.clef)
			}

			let intervalType
			if (config.intervalNames) {
				intervalType = config.intervalNames[QNoteElem.random(config.intervalNames.length-1)]
			}
			if (!intervalType) {
				var deltaStep = 0
				if (config && config.steps && config.steps.length>0) {
					deltaStep = config.steps[QNoteElem.random(config.steps.length-1)]-1
				}else{
					deltaStep = QNoteElem.random(14) //0...7
				}
				var type
				if (config && config.types && config.types.length>0) {
					type = config.types[QNoteElem.random(config.types.length-1)]
				}else{
					type = intervalTypes[QNoteElem.random(intervalTypes.length-1)]
				}
				intervalType = {type: type, step:deltaStep, alter: alter}
			}

			var interval = res.intervalFromRoot(rootNote, intervalType)
			if (interval) {
				// console.log(interval)
				res.name = interval.name
				res.root = interval.root
				res.head = interval.head
				res.step = interval.step
				res.type = interval.type
				return res
			}
		}
	}
	static intervalFromRoot(rootNote, intervalType) {
		let q = new QInterval()
		let interval = q.intervalFromRoot(rootNote, intervalType)
		// console.log(interval)
		return interval
	}
}
export default QInterval;



