import QRhythm from './QRhythm';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';

class QRhythmQuestions extends BaseQuestions {
	/*config={
		level: 1...5
		beats: ['2/4拍','3/4拍','4/4拍','3/8拍','6/8拍']
		measures: ['2','4','6','8']
		rhythmTypes: ['1', '2.', ...]
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (!config.qtypes ||  config.qtypes.includes('Keyboard')) {
			this.genQuestionKeyboard(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('EarTraining')) {
			this.genQuestionEarTraining(questions, config)
		}
		
		return questions
	}
	static genQuestionKeyboard(questions, config) {
		let rhythm = QRhythm.randomRhythm(config)
		let events = this.eventsOfNotes(rhythm.notes, rhythm.beats, rhythm.beatUnit)
		var question = new Question()
		question.desc = '弹奏节奏'
		question.score = {fifths: 0, beats: rhythm.beats, beatUnit: rhythm.beatUnit, notes: rhythm.notes}
		question.midi = {countdown: true, beats: rhythm.beats, beatUnit: rhythm.beatUnit, events:events}
		question.answers = {notes: rhythm.notes}
		question.keyboard = KeyboardType.PlayRhythm
		questions.push(question)
	}
	static genQuestionEarTraining(questions, config) {

		let rhythm = QRhythm.randomRhythm(config)
		let events = this.eventsOfNotes(rhythm.notes, rhythm.beats, rhythm.beatUnit)
		var question = new Question()
		question.desc = '写出听到的节奏'
		question.score = {fifths: 0, beats: rhythm.beats, beatUnit: rhythm.beatUnit} //, notes: rhythm.notes}
		question.midi = {countdown: true, beats: rhythm.beats, beatUnit: rhythm.beatUnit, events:events}
		question.answers = {notes: rhythm.notes}
		question.keyboard = KeyboardType.WriteRhythm
		questions.push(question)
	}
}
export default QRhythmQuestions;



