import React from 'react';
import Glyph from '../components/MusicGlyph';
import MusicMeasure from './MusicMeasure';
import {lineh, staffDistance, measureMarginLeft} from '../config/MusicConfig';
import MusicNote from '../music/MusicNote';

class MusicPart {
	constructor(json) {
		this.name = ''
		this.beats = 0
		this.beatUnit = 0
		this.fifths = 0
		this.clefs = ['treble']
		this.staves = 1
		this.measures = []
		if (json) {
			this.loadFrom(json)
		}else{
			var measure = new MusicMeasure()
			this.measures.push(measure)
		}
	}
	loadFrom(json) {
		this.name = json.name
		this.staves = json.staves
		let measures = json.measures
		this.measures = []
		for (var i = 0; i < measures.length; i++) {
			const m = measures[i]
			var measure = new MusicMeasure()
			if (i===0) {
				this.beats = m.beats ? m.beats : 0
				this.beatUnit = m.beatUnit ? m.beatUnit : 0
				this.clefs = m.clefs ? m.clefs : ['treble']
				this.fifths = m.fifths ? m.fifths : 0
			}else{
				measure.fifths = m.fifths
				measure.beats = m.beats
				measure.beatUnit = m.beatUnit
			}
			measure.staves=m.staves
			this.measures.push(measure)
		};
	}
	getJson() {
		var json={}
		json.name = this.name
		json.staves = this.staves
		json.measures = []
		for (var i = 0; i < this.measures.length; i++) {
			const measure = this.measures[i]
			var item = {staves:measure.staves}
			if (i===0) {
				item.beats = this.beats
				item.beatUnit = this.beatUnit
				item.fifths = this.fifths
				item.clefs = this.clefs
			}else{
				if (measure.fifths) {
					item.fifths = measure.fifths
				}
				if (measure.beats) {
					item.beats = measure.beats
				}
				if (measure.beatUnit) {
					item.beatUnit = measure.beatUnit
				}
			}
			json.measures.push(item)
		}
		return json;
	}
	getStartMeasureIndex(curMeasureIndex, curNoteIndex) {
		var width = 0
		var startm = 0
		for (var i = curMeasureIndex; i >= 0; i--) {
			if (i>=this.measures.length) {
				continue;
			}
			var measure = this.measures[i];
			width += measure.width
			// console.log(width)
			if (width>3840*0.6) {
				startm = i
				break
			}
		}
		var stopm = this.measures.length-1
		width = 0
		for (var m = startm; m < this.measures.length; m++) {
			measure = this.measures[m];
			width += measure.width
			if (width>3840) {
				stopm = m
				break
			}
		}
		return {startm: startm, stopm: stopm}
	}
	prevClef(measureIndex, staffIndex, noteIndex) {
		if (measureIndex>0 || noteIndex>0) {
			var startNoteIndex = noteIndex
			for (var i = measureIndex; i >= 0; i--) {
				const measure = this.measures[i]
				for (var j = startNoteIndex; j >= 0; j--) {
					var note = measure.staves[staffIndex][j]
					if (note.type === 'clef') {
						var clef = note.value
						return clef
					}
				}
				startNoteIndex = 0
			}
		}
		return this.clefs[staffIndex]
	}
	changeClef(measureIndex, staffIndex, noteIndex) {
		var deltaLine = 0
		var clef = this.prevClef(measureIndex, staffIndex, noteIndex)
		
		if (clef==='treble') {
			clef = 'bass'
			deltaLine = 12
		}else{
			clef = 'treble'
			deltaLine = -12
		}
		if (measureIndex===0 && noteIndex===0) {
			this.clefs[staffIndex] = clef
		}else{
			const notes = this.measures[measureIndex].staves[staffIndex]
			var note = notes[noteIndex]
			if (note.type === 'clef') {
				note.value = clef
			}else if (noteIndex>0){
				if (notes[noteIndex-1].type === 'clef') {
					notes[noteIndex-1].type = clef
				}else{
					notes.splice(noteIndex, 0, {type:'clef', value: clef});
				}
			}
		}
		for (var m = measureIndex; m < this.measures.length; m++) {
			const measure = this.measures[m]
			var notes = measure.staves[staffIndex]
			var start = (m === measureIndex) ? noteIndex : 0
			for (var n = start; n < notes.length; n++) {
				const note = notes[n];
				if (note.type === 'clef' && (m>measureIndex || n>start)) {
					return
				}
				if (note && !note.rest && note.elems) {
					for (var i = 0; i < note.elems.length; i++) {
						note.elems[i].line += deltaLine
					}
				}
			}
		}
	}
	setTie(measure, note, elem, measureIndex, staffIndex, noteIndex) {
		var notes = measure.staves[staffIndex]
		var stopNote
		elem.tie.endx = lineh*3
		if (noteIndex<notes.length-1) {
			stopNote = notes[noteIndex+1]
			elem.tie.endx = stopNote.x-note.x
		}else if(measureIndex<this.measures.length-1){
			const stopMeasure = this.measures[measureIndex+1]
			var stopNotes = stopMeasure.staves[staffIndex]
			if (stopNotes.length>0) {
				stopNote = stopNotes[0]
				elem.tie.endx = stopNote.x-note.x+measure.width+measureMarginLeft*2
			}
		}
		if (stopNote && stopNote.elems) {
			for (var stopIndex = 0; stopIndex < stopNote.elems.length; stopIndex++) {
				const stopElem = stopNote.elems[stopIndex]
				if (stopElem.line === elem.line) {
					elem.tieType = 'start'
					stopElem.tieType = 'stop'
					break
				}
			}
		}
	}
	
	addMeasureItems(items, measure, m, x, basey, cursor = {}) {
		var key = items.length
		for (var staffIndex = 0; staffIndex< measure.staves.length; staffIndex++) {
			var notes = measure.staves[staffIndex]
			for (var i = 0; i < notes.length; i++) {
		        var note = notes[i];
		        if (cursor && cursor.measureIndex===m && cursor.staffIndex===staffIndex && (cursor.noteIndex===i || (i===notes.length-1 &&cursor.noteIndex===notes.length))) {
		        	let left = x+note.x
		        	if (cursor.noteIndex !== i) {
		        		left += lineh*2
		        	}
		        	//显示光标
		        	items.push(<Glyph key={key} type='rect' params={{width:lineh*1.2, height:4*lineh}} basey={basey+3.9*lineh} left={left} color='rgba(28,28,255,0.2)' scale={0.6} />)
		        	key += 1
		        }
		        if (!note.rest && note.elems) {
			        for (var e = 0; e < note.elems.length; e++) {
			        	var elem = note.elems[e]
			        	if (cursor && cursor.measureIndex===m && cursor.staffIndex===staffIndex && cursor.noteIndex===i && cursor.elemIndex===e) {
			        		elem.color = 'rgb(255,28,28)'
			        	}else{
				        	elem.color = elem.fill ? elem.fill : null;
			        	}
			        	//tie
				        if (elem.tie) {
				        	this.setTie(measure, note, elem, m, staffIndex, i)
				        }
			        }
		        }else{
		        	if (cursor && cursor.measureIndex===m && cursor.staffIndex===staffIndex && cursor.noteIndex===i) {
						note.color = 'rgb(255,28,28)'
					}else{
						note.color = null;
					}
		        }
		        if (note.tuplet) {
		        	measure.setTuplet(note, notes, i)
		        }
		        note.staff=staffIndex
		        items.push(<Glyph key={key} type={note.type} params={note} basey={basey} left={x+note.x} scale={0.6} />)
		        key += 1
				if (cursor && cursor.append && cursor.appendNote && cursor.measureIndex===m && cursor.noteIndex===i) {
			        items.push(<Glyph key={key} type={cursor.appendNote.type} params={cursor.appendNote} basey={basey} left={x+note.x+lineh*2} color='rgba(255,28,28,0.5)' />)
		        	key += 1
				}
			}
		}
		x += measure.width
		//console.log('addMeasureItems:'+m+' x:'+x)
		if (!this.config || !this.config.hideBarline) {
			items.push(
		        <Glyph 
		          key={key}
		          type='barline'
		          left={measure.barline ? x-lineh/2 : x}
		          basey={basey}
		          params={{value:measure.barline ? measure.barline : '|', line:-4, staves: this.staves}}
		        >
		        </Glyph>			
	        )
		}
	}
	addFifthsItems(items, x, basey, fifths) {
		// var items = []
	    //key signature
	    if (fifths && fifths !== 0) {
			var y = basey
			var key = items.length
	    	for (var i = 0; i < this.staves; i++) {
		    	items.push(<Glyph key={key} type='fifths' params={{fifths: fifths, clef:this.clefs[i]}} basey={y} left={x} />)
			    key += 1
			    y += staffDistance+4*lineh
	    	}
		    x += lineh*Math.abs(fifths)
		    x += 1*lineh
	    }
	    return x
	}
	addBeatsItems(items, x, basey, beats, beatUnit) {
	    //beats
	    if (beats && beats>0 && beatUnit && beatUnit>0) {
	    	var y = basey
			var key = items.length
	    	for (var i = 0; i < this.staves; i++) {
		    	items.push(<Glyph key={key} type='beats' params={{value: beats, line:2}} basey={y} left={x} />)
		    	items.push(<Glyph key={key+1} type='beatUnit' params={{value: beatUnit, line:-2}} basey={y} left={x} />)
			    key += 2
			    y += staffDistance+4*lineh
			}
		    x += lineh*2
	    }
	    return x
	}
	// curMeasureIndex, curNoteIndex, curElemIndex, appendNote
	addItems(items, x, basey, startm, stopm, cursor = {}) {
		//clef
		var partNamey = -6
		if (this.staves>1) {
			partNamey-=staffDistance/lineh+4
		}
		items.push(<Glyph key={items.length} type='text' params={{text: this.name, x: lineh/2, line: partNamey}} fontSize={lineh} basey={basey} left={x} />)
		if (!this.config || !this.config.hideClef) {
			for (var i = 0; i < this.staves; i++) {
				var clef = i<this.clefs.length ? this.clefs[i] : 'bass'
			    items.push(<Glyph key={items.length} type='clef' params={{value: clef, line:clef==='bass' ? 2 : -2}} basey={basey+i*(staffDistance+4*lineh)} left={x} />)
			}
		    x += 4*lineh;
		}

	    //key signature
    	x = this.addFifthsItems(items, x, basey, this.fifths)
	    //beats
    	x = this.addBeatsItems(items, x, basey, this.beats, this.beatUnit)
	    x += 1*lineh
	    //measures
	    for (var m = startm; m <= stopm; m++) {
			var measure = this.measures[m];
			if (!measure) {
				break
			}
			if (m>0) {
				//key signature
		    	x = this.addFifthsItems(items, x, basey, measure.fifths)
			    //beats
		    	x = this.addBeatsItems(items, x, basey, measure.beats, measure.beatUnit)
			}
			this.addMeasureItems(items, measure, m, x, basey, cursor)
			x += measure.width
			x += 20
	    };
	}

	insertNoteTuplet(notePos) {
		const measure = this.measures[notePos.measureIndex]
		const note = measure.staves[notePos.staffIndex][notePos.noteIndex]
		if (note.tuplet) {
			note.tuplet.count += 1
			if (note.tuplet.count > 5) {
				note.tuplet = null
			}
		}else{
			note.tuplet = {count: 2}
		}
	}
	insertNoteTie(notePos) {
		const measure = this.measures[notePos.measureIndex]
		const note = measure.staves[notePos.staffIndex][notePos.noteIndex]
		if (!note.elems) {
			return
		}
		if (notePos.elemIndex>=note.elems.length) {
			notePos.elemIndex = 0
		}
		const elem = note.elems[notePos.elemIndex]
		if (elem.tie) {
			elem.tie = null
			return
		}
		elem.tie = {}
	}
	appendMeasure() {
		const measure = new MusicMeasure()
		measure.staves = [[{note:1, rest: true}]]
	    this.measures.push(measure)
	}
	getAlterOfStep(fifths) {
		let alterOfStep = {}
		if (fifths>0) {
			//F,C,G,D,A,E,B
			//3,0,4,1,5,2,6
			let steps = [3,0,4,1,5,2,6]
			for (let i=0; i<fifths; i++) {
				alterOfStep[steps[i]] = 1
			}
		}else if (fifths<0) {
			let steps = [6,2,5,1,4,0,3]
			for (let i=0; i<fifths; i++) {
				alterOfStep[steps[i]] = -1
			}
		}
		return alterOfStep
	}
	
	getPartNotes() {
		let allNotes = []
		let fifths = 0
		let clefs = this.clefs
		for (let measure of this.measures) {
			if (measure.fifths || measure.fifths===0) {
				fifths = measure.fifths
			}
			if (measure.clefs) {
				for (var i = measure.clefs.length - 1; i >= 0; i--) {
					let clef = measure.clefs[i]
					clefs[i]=clef
				}
			}
			let alterOfStep = this.getAlterOfStep(fifths)
			let alterOfLine = {}
			let notes = measure.staves[0]
			for (let note of notes) {
				if (!note.elems) {
					continue
				}
				for (let elem of note.elems) {
					if (elem.accidental) {
						alterOfLine[elem.line] = elem.alter
					}else {
						if (alterOfLine[elem.line]) {
							elem.alter = alterOfLine[elem.line]
						}else {
							elem.step = MusicNote.stepFromLine(elem.line, clefs[0])
							if (alterOfStep[elem.step]) {
								elem.alter = alterOfStep[elem.step]
							}else {
								elem.alter = 0
							}
						}
					}
				}
			}
			allNotes.push(...notes)
		}
		return allNotes
	}
}
export default MusicPart;



