import React from 'react';
import {lineh, staffDistance} from '../config/MusicConfig';
import Musfont, {Line, ExLine} from './MusicGlyphFont';
import {Beam, Accidental} from '../music/MusicTypes';

//art
const artDot = '\u01c7'

// note
const noteHeadDouble = '\u0140'
const noteWhole = '\u0141'
const noteHead4 = '\u0174'
const noteHead2 = '\u017c'

export const accidentalElems = {'sharp':'\u0121', 'flat':'\u0122', 'natural':'\u0123', 'sharp-sharp':'\u0124', 'flat-flat':'\u0125'}

const upNotes = {'d2':'\u0140', 1:'\u0141',2:'\u0142',4:'\u0143',8:'\u0144',16:'\u0145',32:'\u0146',64:'\u0147',128:'\u0148'};
// const downNotes = {1:'\u0151',2:'\u0152',4:'\u0153',8:'\u0154',16:'\u0155',32:'\u0156',64:'\u0157',128:'\u0158'};
const upTails = {8:'\u0149',16:'\u014a',32:'\u014b',64:'\u014c',128:'\u014d'};
const downTails = {8:'\u0159',16:'\u015a',32:'\u015b',64:'\u015c',128:'\u015d'};
const restNotes = {'d2':'\u0160', 1:'\u0161',2:'\u0162',4:'\u0163',8:'\u0164',16:'\u0165',32:'\u0166',64:'\u0167',128:'\u0168'};

class CurveLine extends React.Component {
  render() {
      const x1 = this.props.x1
      const x2 = this.props.x2
      const cpx1 = x1+(x2-x1)/4
      const cpx2 = x2-(x2-x1)/4
      var y1, y2, cpy1, cpy2
      if (this.props.above) {
        y1 = this.props.y1-lineh/2
        y2 = this.props.y2-lineh/2
        cpy1 = y1-lineh
        cpy2 = y2-lineh
      }else{
        y1 = this.props.y1+lineh/2
        y2 = this.props.y2+lineh/2
        cpy1 = y1+lineh
        cpy2 = y2+lineh
      }
      var d = 'M'+x1+' '+y1+' C '+cpx1+' '+cpy1+', '+cpx2+' '+cpy2+', '+x2+' '+y2
      cpy1 -= lineh/5
      cpy2 -= lineh/5
      d += ' C '+cpx2+' '+cpy2+', '+cpx1+' '+cpy1+', '+x1+' '+y1
      // console.log(d)
      const tie = <svg style={{position: 'absolute'}} left={1} width={x2} height={48*lineh} > <path d={d} fill='black'/> </svg>
      return tie
  }
}

class GlyphNote extends React.Component {
  //stem, tail, beams
  noteStemTails() {
    const num = this.props.params.note;
    var tails=[]
    if (num==='d2' || num<2 || this.props.params.rest) {
      return
    }
    const fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
    const topLine=this.props.params.topLine
    const bottomLine=this.props.params.bottomLine
    var stemx = 0.5
    //stem
    var lineWidth = lineh>10 ? lineh/10 : 1
    if (this.props.params.stemUp) {
      const stemTop = this.yOfLine(this.props.params.stemLine-12);
      const height = (this.props.params.stemLine-bottomLine-0.3)*lineh/2;
      stemx = lineh*1.1+1
      if (!stemTop) {
        console.log(this.props)
      }
      tails.push(<Line key={1} left={stemx} width={lineWidth} height={height} top={stemTop} color={document.body.style.color} />)
    }else{
      const stemTop = this.yOfLine(this.props.params.topLine-12);
      const height = (topLine - this.props.params.stemLine)*lineh/2;
      if (!height) {
        console.log(this.props)
      }
      tails.push(<Line key={1} left={stemx} width={lineWidth} height={height} top={stemTop} color={document.body.style.color} />)
    }
    //tail
    if (num>4) {
      if (this.props.params.beams) {
        for (var beamIndex = 0; beamIndex < this.props.params.beams.length; beamIndex++) {
          const beam = this.props.params.beams[beamIndex]
          if (beam.type !== Beam.Begin && beam.type !== Beam.Backward && beam.type !== Beam.Forward) {
            continue;
          }

          const y1 = this.yOfLine(this.props.params.stemLine-12);
          var y2 = this.yOfLine(beam.endline-12)
          // console.log('draw beam')
          // console.log(beam)
          var x1 = stemx
          var x2
          if (beam.type === Beam.Forward) {
            x2 = stemx+lineh
            y2 = y1
          }else if (beam.type === Beam.Backward) {
            x1 = stemx-lineh
            x2 = stemx
            y2 = y1
          }else {
            x2 = this.props.params.stemUp ? beam.endx+lineh*1.2 : beam.endx+lineh*0.2
          }

          const delta = this.props.params.stemUp ? lineh*0.5 : -lineh*0.5
          const offsety = beamIndex*delta
          tails.push(<ExLine key={3+beamIndex} x1={x1} y1={y1+offsety} x2={x2} y2={y2+offsety} width={lineh*0.35} color={document.body.style.color} />)
        }
      }else if (!this.props.params.beams){
        const stemLine = (this.props.params.stemUp) ? 7 : -7;
        const text = (this.props.params.stemUp) ? upTails[num] : downTails[num]
        const y = this.yOfLine(this.props.params.stemLine-stemLine)
        tails.push(<Musfont key={3} left={1} top={y} glyph={text} fontSize={fontSize}/>)
      }
    }
    return tails
  }
  noteMoreLines() {
    var lines = []
    const topLine=this.props.params.topLine
    const bottomLine=this.props.params.bottomLine
    if (topLine<6 && bottomLine>-6) {
      return lines
    }
    // console.log('bottomLine:'+bottomLine)
    const width = this.props.params.note===1 ? lineh*2.1 : lineh*1.6
    const total = topLine>5 ? topLine : -bottomLine;
    for (var lineIndex = 3; lineIndex <= total/2; lineIndex++) {
      var lineTop = 0
      if (bottomLine<-5) {
        lineTop = this.yOfLine(-(lineIndex+5.85)*2);
      }else{
        lineTop = this.yOfLine((lineIndex-5.85)*2);
      }
      lines.push(<Line key={lineIndex} left={-lineh*0.2} width={width} height={lineh/10} top={lineTop} />)
    }
    return lines
  }
  yOfLine(line) {
    var top = this.props.basey-line*lineh/2
    if (this.props.params.staff>0) {
      top += staffDistance+4*lineh
    }
    return top;
  }
  prevAccElemIndex(elemIndex, elems) {
    if (elemIndex>0) {
      const prevElem = elems[elemIndex-1]
      if (prevElem.accidental) {
        return elemIndex-1
      }else if (elemIndex>1) {
        const prevElem = elems[elemIndex-2]
        if (prevElem.accidental) {
          return elemIndex-2
        }
      }
    }
  }
  setElemsAccx(elems) {
    var accElems = elems.filter((elem) => elem.accidental )
    for (var i = 0; i < accElems.length; i++) {
      const elem = accElems[i]
      if (elem.accidental) {
        elem.accx = this.accOffsetx(elem, i, accElems)
      }
    }
  }
  accOffsetx(elem, elemIndex, elems) {
    var accWidth = elem.accidental===Accidental.doubleFlat ? lineh*1.2 : elem.accidental===Accidental.doubleSharp ? lineh : lineh*0.9
    var accx = -accWidth
    if (elemIndex>0) {
      const prevElem1 = elems[elemIndex-1]
      if (prevElem1.line+6>elem.line && prevElem1.accx) {
        // accx -= lineh
        accx = prevElem1.accx-accWidth
        if (elemIndex>1) {
          const prevElem2 = elems[elemIndex-2]
          if (prevElem2.accx) {
            accx = prevElem1.accx-accWidth
            if (elemIndex>2) {
              const prevElem3 = elems[elemIndex-3]
              if (prevElem3.accx) {
                  accx = prevElem3.accx
              }
            }
          }
        }
      }
    }
    elem.accx = accx
    return accx
  }
  noteMargin(elems) {
    var margin = {left:1, right:1}
    for (var i = 0; i < elems.length; i++) {
      const elem = elems[i]
      if (this.props.params.stemUp && i>0 && (elems[i-1].line+1===elem.line || elems[i-1].line===elem.line)) {
        margin.right = lineh*1.1
      }else if(!this.props.params.stemUp && i<elems.length-1 && (elems[i+1].line===elem.line+1 || elems[i+1].line===elem.line)) {
        margin.left = -lineh*1.1
      }
    }
    return margin
  }
  noteHeads() {
    const fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
    const num = this.props.params.note;
    // var text = line && line>0 && num>1 ? downNotes[num] : upNotes[num];
    var text = num===1 ? noteWhole : num===2 ? noteHead2 : num==='d2' ? noteHeadDouble : noteHead4
    //elems
    var heads=[]
    if (this.props.params.rest) {

      text = restNotes[this.props.params.note];
      const restLine = (this.props.params.elems && this.props.params.elems[0].line) ? this.props.params.elems[0].line : 0
      //top = basey+(-restLine)*lineh/2
      let top = this.yOfLine(restLine);
      if (num>4) {
        top += lineh/2
      }
      if (this.props.params.staff>0) {
        top += staffDistance+4*lineh
      }
      if (this.props.params.dots>0) {
        for (let i = 0; i < this.props.params.dots; i++) {
          text += ' '+artDot
        }
      }
      let color = this.props.color ? this.props.color : this.props.params.color ? this.props.params.color : null
      heads.push(<Musfont key={0} left={1} top={top} glyph={text} color={color} fontSize={fontSize} onClick={this.props.onClick} />)
    }else if (this.props.params.elems) {
      let elems = [...this.props.params.elems].sort((a,b)=>a.line-b.line)
      // var elems = this.props.params.elems.sort((a,b)=>a.line-b.line)
      this.setElemsAccx(elems)
      const margin = this.noteMargin(elems)
      for (let i = 0; i < elems.length; i++) {
        const elem = elems[i]
        const line = elem.line
        let top = this.yOfLine(line); // this.props.basey-line*lineh/2
        
        let notex = 1
        //note head
        let prevElem = i>0 ? elems[i-1] : null
        if (this.props.params.stemUp && i>0 && (prevElem.line+1===elem.line || prevElem.line===elem.line)) {
          if (!(prevElem.line+1===elem.line && i>1 && elems[i-2].line+2===elem.line)) {
            notex += lineh*1.1
          }
        }else if(!this.props.params.stemUp && i<elems.length-1 && (elems[i+1].line===elem.line+1 || elems[i+1].line===elem.line)) {
          notex -= lineh*1.1
        }
        heads.push(<Musfont key={100*i} left={notex} top={top} glyph={text} color={elem.color} fontSize={fontSize} onClick={this.props.onClick} />)
        //accidental
        if (elem.accidental) {
          const accx = elem.accx+margin.left;
          const acc=<Musfont key={10*i+1} left={accx} top={top+lineh*0.3} glyph={accidentalElems[elem.accidental]} color={elem.color} fontSize={fontSize*0.95}/>
          heads.push(acc)
        }else{
          elem.accx = 0
        }
        //dots
        if (this.props.params.dots > 0) {
          const dotx = margin.right+lineh*1.5
          for (var dotIndex = 0; dotIndex < this.props.params.dots; dotIndex++) {
            const dot = <Musfont key={10*i+2+dotIndex*2} left={dotx+dotIndex*0.7*lineh} top={top+(line%2===0 ? -lineh/2 : 0)} glyph={artDot} color={elem.color} fontSize={fontSize}/>
            heads.push(dot)
          }
        }
        //tie
        if (elem.tie) {
          const x1 = lineh
          const x2 = elem.tie.endx
          const y1 = this.yOfLine(line-12)
          const y2 = y1
          const above = !this.props.params.stemUp || i>0
          const tie = <CurveLine key={10*i+3} x1={x1} x2={x2} y1={y1} y2={y2} above={above} />
          heads.push(tie)
          // console.log('basey:'+this.props.basey+' line:'+line+' y:'+y1)
        }
      }
    }
    const tuplet = this.props.params.tuplet
    if (tuplet) {
      const x1 = lineh*0.5
      const x2 = tuplet.endx+x1
      const y1 = this.yOfLine(tuplet.beginline)+lineh*4.5
      const y2 = this.yOfLine(tuplet.endline)+lineh*4.5
      const k = (y2-y1)/(x2-x1)
      const midx1 = (x1+x2)/2-lineh/2
      const midx2 = (x1+x2)/2+lineh/2
      const midy1 = k*(midx1-x1)+y1
      const midy2 = k*(midx2-x1)+y1
      // console.log(tuplet)
      heads.push(<Line key={100} left={x1} width={lineh/10} height={lineh/2} top={y1} color={document.body.style.color} />)
      heads.push(<Line key={101} left={x2} width={lineh/10} height={lineh/2} top={y2} color={document.body.style.color} />)
      heads.push(<ExLine key={102} x1={x1} y1={y1} x2={midx1} y2={midy1} width={lineh*0.1} color={document.body.style.color} />)
      heads.push(<ExLine key={103} x1={midx2} y1={midy2} x2={x2} y2={y2} width={lineh*0.1} color={document.body.style.color} />)
      heads.push(<div key={104} style={{position: 'absolute', left: midx1+lineh*0.3, top:midy1-lineh/2, fontSize: lineh}}>{tuplet.count}</div>)
    }
    return heads
  }

  render() {
    if (!this.props.params.elems && !this.props.params.rest) {
      const text = upNotes[this.props.params.note]
      const fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
      return (<Musfont left={this.props.left} top={this.props.basey} glyph={text} color={this.props.color} fontSize={fontSize} />)
    }else{
      var elems=this.noteHeads()
      const tails=this.noteStemTails()
      const lines = this.noteMoreLines()
      let flag = null
      if (this.props.params.flag) {
        flag = <div style={{position: 'absolute', left: 0, top: 0}}>{this.props.params.flag}</div>
      }
      return (
        <div style={{position:'absolute', left: this.props.left, top: 0}}>
          {lines}
          {tails}
          {elems}
          {flag}
        </div>
        )
    }
  }
}

export default GlyphNote;




