import React from 'react'
import Toolbar, {iconfont, color} from "../components/Toolbar";

class ListView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      list: props.list,
      index: 0
    }
  }
  takeAction(action) {
    console.log(action)
    var index = this.state.index
    console.log(index)
    switch (action.type) {
      case 'index':
        index = index+action.offset
        console.log(index)
        if (index>=0 && index<this.state.list.length) {
          this.setState(prevState => ({
            index: index
          }))
        }
        break
      case 'ok':
        this.props.action(this.state.index, this.state.list[this.state.index])
        break
      default:
        break
    }
  }
  listToolbar(list) {
    var buttons = [
      {iconfont:iconfont.arrowUp, key: 78, action:{type: "index", offset: -1}, background: color.blue},
      {iconfont:iconfont.arrowDown, key: 80, action:{type: "index", offset: 1}, background: color.blue},
      {title:"ok", key: 85, action:{type: "ok"}, background: color.blue},
      {title:"cancel", key: 87, action:{type: "ok"}, background: color.blue},
      ]
    return <Toolbar items={buttons} action={(action) => this.takeAction(action)}/>
  }
  onclick(item) {
    console.log(item)
    this.props.action(item.index, item.value)
  }
	render() {
    const width =  window.document.body.offsetWidth
    var items = []
    for (var i = 0; i < this.state.list.length; i++) {
      let item = this.state.list[i]
      var style = {width: '100%', margin: '5px 1px'}
      if (this.state.index === i) {
        style.background = "rgb(115, 205, 255)"
      }
      items.push(<div key={i} style={style} onClick={(e) => this.onclick({index: i, value: item})}>{item}</div>)
    }
		return(
      <div style={{position: "absolute", left: 0, bottom: 0}}>
	 	     <div style={{position: "absolute", left: width*0.6, bottom: this.props.bottom, width: width/4, border: '2px solid #ddd', background: '#eee'}}>
		      {items}
  		  </div>
        {this.listToolbar()}
      </div>
		);
	}
}

export default ListView;



