
class MusicNote {
	//return {octave: int, step: int}
	static stepFromLine(line, clef) {
		let yinhu = line+7*5
		if (clef === 'treble') {
			//line -6 中央C
			yinhu += 6
		}else if (clef === 'bass') {
			//line 6 中央C: 60
			yinhu += -6
		}else if (clef === 'soprano') {
			//1线是C
			//line -4 C
			yinhu += 4
		}else if (clef === 'mezzosoprano') {
			//2线是C
			//line -2 C
			yinhu += 2
		}else if (clef === 'alto') {
			//3线是C
			//line 0 C
			yinhu += 0
		}else if (clef === 'tenor') {
			//4线是C
			//line 2 C
			yinhu += -2
		}else if (clef === 'baritone') {
			//5线是C
			//line 4 C
			yinhu += -4
		}
		let step = yinhu%7
		let octave = Math.floor(yinhu/7)
		return {step: step, octave: octave}
		// let note = new QNoteElem()
		// note.octave = Math.floor(yinhu/7)
		// note.step = yinhu%7
		// note.setNoteValueLineAcc(clef)
		// return note
	}
	static noteDuration(note) {
		if (!note.note) {
			return 0
		}
		let duration = 480*4/note.note;
		if (note.dots===1) {
			duration *= 1.5;
		}else if (note.dots===2) {
			duration *= 1.75;
		}
		return duration;
	}
	static noteTopBottomLine(note) {
		let topLine=-100
		let bottomLine=100
		if (note.elems) {
			for (let elem of note.elems) {
				const line = elem.line ? elem.line : 0
				if (line>topLine) {
					topLine = line
				}
				if (line<bottomLine) {
					bottomLine = line
				}
			}
		}else{
			topLine = 0
			bottomLine = 0
		}
		note.topLine=topLine
		note.bottomLine=bottomLine
		return {topLine: note.topLine, bottomLine: note.bottomLine}
	}
	static tailsOfNote(note) {
		if (note.note===8) {
			return 1
		}else if (note.note===16) {
			return 2
		}else if (note.note===32) {
			return 3
		}else if (note.note===64) {
			return 4
		}else if (note.note===128) {
			return 5
		}
		return 0
	}
	/*
	以下组成浮点音符：
	2连音：每个音的时值*3/n
	4连音：每个音的时值*3/n 
	8连音：每个音的时值*6/n
	16连音：每个音的时值*12/n

	以下组成普通音符：
	3连音：每个音的时值*2/n
	5,6,7连音：每个音的时值*4/n
	9,10,11,12,13,14,15连音：每个音的时值*8/n
	17-31连音：每个音的时值*16/n
	33-63连音：每个音的时值*32/n
	*/
	static calcNoteDuration(notes) {
		let tupletStartindex = 0
		let tupletCount = 0
		for (let i = 0; i < notes.length; i++) {
			let note = notes[i]
			let duration = MusicNote.noteDuration(note)

			if (note.tuplet && note.tuplet.count) {
				tupletStartindex = i
				tupletCount = note.tuplet.count
			}
			if (tupletCount>0 && i<tupletStartindex+tupletCount) {
				if (tupletCount===2 || tupletCount===4) {
					duration *= 3
				}else if (tupletCount===3) {
					duration *= 2
				}else if (tupletCount===8) {
					duration *= 6
				}else if (tupletCount===16) {
					duration *= 12
				}else if (tupletCount===5 || tupletCount===6 || tupletCount===7) {
					duration *= 4
				}else if (tupletCount>=9 && tupletCount<=15) {
					duration *= 8
				}else if (tupletCount>=17 && tupletCount<=31) {
					duration *= 16
				}else if (tupletCount>=33 && tupletCount<=63) {
					duration *= 32
				}else{
					console.log('error, unknow tuplet '+tupletCount)
				}
				duration /= tupletCount
			}
			note.duration = duration
		}
	}
}

export default MusicNote;



