import MusicXmlParser from './MusicXmlParser';
import MusicXmlCreater from './MusicXmlCreater';

const xmlHeader = `<?xml version='1.0' encoding='UTF-8' standalone='no' ?>\n<!DOCTYPE score-partwise PUBLIC '-//Recordare//DTD MusicXML 3.0 Partwise//EN' 'http://www.musicxml.org/dtds/partwise.dtd'>\n`
export function saveScore(score) {
	let xml = new MusicXmlCreater()
	let obj = xml.createXML(score)
	const doc = new XMLSerializer().serializeToString(obj)
	const str = xmlHeader+doc
	return str
}

export function loadScore(xmlString) {
	//创建文档对象
	let parser=new DOMParser();
	let xmlDoc=parser.parseFromString(xmlString,'text/xml');
	
	let xml = new MusicXmlParser()
	return xml.parseXml(xmlDoc)
}



