import React, { Component } from 'react';

class NavHeader extends Component {
	render() {
		var style = {
			// width: 120,
			height: 24,
			padding: 5,
			margin: 4,
			float: 'left',
			//boxShadow: '0 0 1px #909090',
			textAlign: 'left',
			}
		return (
			<div style={{display: 'table', width: '100%'}}>
				<div style={style} onClick={(e) => {window.theApp.popWindow()}}>&lt; 返回 </div>
				<div style={{textAlign: 'center', padding:10, fontWeight: 'bold', fontSize: 'larger'}}>Find音乐学院基本乐科 v{window.ver}</div>
			</div>
		)
	}
}

export default NavHeader;



