
const noteTypes = {1: 'whole', 2: 'half', 4: 'quarter', 8: 'eighth', 16: '16th', 32: '32nd'}
class MusicXmlCreater {
	constructor() {
		this.divisions = 1024
		this.xmlns = 'http://www.w3.org/2000/svg';
		this.xmlDOM = document.implementation.createDocument(this.xmlns, 'xml', null);
	}
	createElem(tag, text, attrs) {
		let elem = this.xmlDOM.createElement(tag)
		if (text) {
			elem.appendChild(this.xmlDOM.createTextNode(text))
		}
		if (attrs) {
			for (let key in attrs){
				elem.setAttribute(key, attrs[key])
			}
		}
		return elem
	}
	pitchOfNote(clef, elem) {
		let pitch = {accidental: elem.accidental}
		if (elem.accidental === 'sharp') {
			pitch.alter = 1
		}else if (elem.accidental === 'sharp-sharp') {
			pitch.alter = 2
		}else if (elem.accidental === 'flat') {
			pitch.alter = -1
		}else if (elem.accidental === 'flat-flat') {
			pitch.alter = -2
		}
		let line
		if (clef === 'treble') {
			//-6 ... 0 octave:4
			line = elem.line+34
		}else{
			//6 ... 12 octave:4
			line = elem.line+22
		}
		pitch.octave = Math.floor(line/7)
		const steps = ['C', 'D', 'E', 'F', 'G', 'A', 'B']
		pitch.step = steps[line%7]
		return pitch
	}
	/*
	'(
	((grace,(chord?,(pitch|unpitched|rest)),(tie,tie?)?)|
	(cue,(chord?,(pitch|unpitched|rest)),duration)|
	(chord?,(pitch|unpitched|rest),duration,(tie,tie?)?)),instrument?,
		(footnote?,level?,voice?),type?,dot*,accidental?,
		time-modification?,stem?,notehead?,
		notehead-text?,staff?,beam*,notations*,lyric*,play?
	)'
	*/
	createNoteXml(clef, ticks, note, staff, elemIndex) {
		let noteElem = this.xmlDOM.createElement('note')
		let elem
		if (note.rest) {
			noteElem.appendChild(this.createElem('rest'))
		}else{
			if (elemIndex>0) {
				noteElem.appendChild(this.createElem('chord'))
			}
			elem = note.elems[elemIndex]
			let pitch = this.xmlDOM.createElement('pitch')
			noteElem.appendChild(pitch)

			const val = this.pitchOfNote(clef, elem)
			pitch.appendChild(this.createElem('step', val.step))
			if (val.alter) {
				pitch.appendChild(this.createElem('alter', val.alter))
			}
			pitch.appendChild(this.createElem('octave', val.octave))
		}
		noteElem.appendChild(this.createElem('duration', ticks*this.divisions/480))
		noteElem.appendChild(this.createElem('voice', 1))
		noteElem.appendChild(this.createElem('type', noteTypes[note.note]))
		if (note.dots) {
			for (let i = 0; i < note.dots; i++) {
				noteElem.appendChild(this.createElem('dot'))
			}
		}
		if (elem && elem.accidental) {
			noteElem.appendChild(this.createElem('accidental', elem.accidental))
		}
		noteElem.appendChild(this.createElem('staff', staff))
		if (note.beams) {
			for (let b = 0; b < note.beams.length; b++) {
				const beam = note.beams[b]
				noteElem.appendChild(this.createElem('beam', beam.type, {number:b+1}))
			}
		}
		if ((elem && elem.tieType) || (elemIndex===0 && note.tupletType)) {
			const notations = this.createElem('notations')
			noteElem.appendChild(notations)
			if (elem.tieType) {
				noteElem.appendChild(this.createElem('tie', null, {type:elem.tieType}))
				notations.appendChild(this.createElem('tied', null, {type: elem.tieType}))
			}
			if (note.tupletType) {
				notations.appendChild(this.createElem('tuplet', null, {type: note.tupletType}))
			}
		}
		return noteElem
	}
	createClefElem(clef, staff) {
		let clefElem = this.createElem('clef', null, {number: staff})
		if (clef==='treble') {
			clefElem.appendChild(this.createElem('sign', 'G'))
			clefElem.appendChild(this.createElem('line', 2))
		}else{
			clefElem.appendChild(this.createElem('sign', 'F'))
			clefElem.appendChild(this.createElem('line', 4))
		}
		return clefElem
	}
	createAttributesElem(part, measureIndex) {
		let attributesElem = this.xmlDOM.createElement('attributes')
		if (measureIndex===0) {
			attributesElem.appendChild(this.createElem('divisions', this.divisions))
		}
		//fifths
		const measure = part.measures[measureIndex]
		if (measure.fifths) {
			let key = this.xmlDOM.createElement('key')
			attributesElem.appendChild(key)
			key.appendChild(this.createElem('fifths', measure.fifths))
			key.appendChild(this.createElem('mode', 'major'))
		}
		//time
		if (measure.beats && measure.beatUnit) {
			let time = this.xmlDOM.createElement('time')
			attributesElem.appendChild(time)
			if (measure.beats !== 0) {
				time.appendChild(this.createElem('beats', measure.beats))
				time.appendChild(this.createElem('beat-type', measure.beatUnit))
			}else{
				time.appendChild(this.createElem('senza-misura'))
			}
		}
		if (measureIndex===0) {
			attributesElem.appendChild(this.createElem('staves', part.staves))
		}
	
		//clef
		for (let i = 0; i < part.staves; i++) {
			let clef = null
			if(measureIndex===0){
				clef = part.clefs[i]
			}else if (i<measure.staves.length) {
				let firstNote = measure.staves[i][0]
				if (firstNote && firstNote.type === 'clef') {
					clef = firstNote.value
				}
			}
			if (clef) {
				let clefElem = this.createClefElem(clef, i+1)
				attributesElem.appendChild(clefElem)
			}
		}
		if (attributesElem.children.length===0) {
			return null
		}
		return attributesElem
	}
	/*
	<backup>
		<duration>3072</duration>
	</backup>
	*/
	createBackupXml(ticks) {
		let elem = this.xmlDOM.createElement('backup')
		let duration = ticks*this.divisions/480
		elem.appendChild(this.createElem('duration', duration))
		return elem
	}
	createMeasureXml(part, measureIndex, number) {
		let measureElem = this.createElem('measure', null, {number: number})
		let attributesElem = this.createAttributesElem(part, measureIndex)
		if(attributesElem) {
			measureElem.appendChild(attributesElem)
		}
		
		const measure = part.measures[measureIndex]
		for (let staffIndex = 0; staffIndex < measure.staves.length; staffIndex++) {
			let notes = measure.staves[staffIndex]
			let ticks = 0
			for (let note of notes) {
				let noteTicks = measure.noteDuration(note)
				if (note.type === 'clef') {
					noteTicks = 0
					attributesElem = this.xmlDOM.createElement('attributes')
					const clefElem = this.createClefElem(note.value, staffIndex+1)
					attributesElem.appendChild(clefElem)
					measureElem.appendChild(attributesElem)
				}else if (note.rest) {
					const noteElem = this.createNoteXml(part.clefs[staffIndex], noteTicks, note, staffIndex+1)
					measureElem.appendChild(noteElem)
				}else if (note.elems) {
					for (let e = 0; e < note.elems.length; e++) {
						const noteElem = this.createNoteXml(part.clefs[staffIndex], noteTicks, note, staffIndex+1, e)
						measureElem.appendChild(noteElem)
					}
				}
				ticks += noteTicks
			}
			if (staffIndex < measure.staves.length-1) {
				const backupElem = this.createBackupXml(ticks)
				measureElem.appendChild(backupElem)
			}
		}
		return measureElem
	}
	createXML(score) {
		this.score = score
		let s = this.createElem('score-partwise', null, {version: '3.0'})
		//work
		if (score.title) {
			let work = this.xmlDOM.createElement('work')
			s.appendChild(work);
			let workTitle = this.xmlDOM.createElement('movement-title')
			work.appendChild(workTitle)
			workTitle.appendChild(this.xmlDOM.createTextNode(score.title))
		}
		//part-list
		let partList = this.xmlDOM.createElement('part-list')
		s.appendChild(partList)
		for (let i = 0; i < score.parts.length; i++) {
			let part = score.parts[i]
			let id = 'P'+i
			let scorePart = this.createElem('score-part', null, {id:id})
			partList.appendChild(scorePart)
			scorePart.appendChild(this.createElem('part-name',part.name))
		}
		//part
		for (let j = 0; j < score.parts.length; j++) {
			let part = score.parts[j]
			let partElem = this.createElem('part', null, {id: 'P'+j})
			s.appendChild(partElem)
			//measures
			for (let m = 0; m < part.measures.length; m++) {
				const measureElem = this.createMeasureXml(part, m, m+1)
				partElem.appendChild(measureElem)
			}
		}
		return s;
	}
}

export default MusicXmlCreater;



