import React from 'react';
import MusicMeasure from './MusicMeasure';
import MusicPart from './MusicPart';
import Glyph from '../components/MusicGlyph';
import {lineh, systemDistance, staffDistance, setXPerQuarter} from '../config/MusicConfig';
import {Accidental} from '../music/MusicTypes';
import MusicNote from '../music/MusicNote';

export const instruments = ['钢琴', '小提琴', '大提琴', '法国号', '双簧管']

class MusicScore {
	constructor(json) {
		this.title = ''
		this.parts = []
		this.max_measure = 0
		if (json) {
			this.loadFrom(json)
		}
		this.cursor = {
			partIndex: 0,
			staffIndex: 0,
			measureIndex: 0,
			noteIndex: 0,
			elemIndex: 0,
			append: false,
			showCursor: false,
			appendNote: {note: 4, rest: false, line: -1}
		}
		this.config = {
			autoBeam: true,
		}
	}
	get inputNoteType() {
		return this.cursor.appendNote.note
	}
	get inputNoteRest() {
		return this.cursor.appendNote.rest
	}
	set inputNoteRest(rest) {
		this.cursor.appendNote.rest = rest
	}
	/*
	config = {
		clef: 'treble',
		fifths: 0,
		hideFiveline: true,
		hideClef: true,
		autoBeam: false,
		xPerQuarter: null,
	}
	*/
	static createScore(notes, config={}) {
		var score = new MusicScore()
	    score.config = config
	    setXPerQuarter(config.xPerQuarter)

	    var part = new MusicPart()
	    score.parts.push(part)
	    part.config = config
	    part.clefs=[config.clef ? config.clef : 'treble']
	    part.staves=1
	    part.fifths=config.fifths ? config.fifths : 0
	    if (config.beats) {
	    	part.beats = config.beats
	    }
	    if (config.beatUnit) {
	    	part.beatUnit = config.beatUnit
	    }
	    if (notes) {
	    	score.setPartNotes(part, notes)
			// part.measures[0].staves = [notes]
	    }else{
	    	part.measures[0].staves = [[]]
	    }
	    return score
	}
	
	setPartNotes(part, notes) {
		MusicNote.calcNoteDuration(notes)
		if (part.beats===0 || part.beatUnit===0 || !notes || notes.length<1) {
			part.measures = [new MusicMeasure()]
			part.measures[0].staves = [notes]
	    }else{
		    let measureDuration = part.beats*480*4/part.beatUnit
		    part.measures = []
		    let measure = new MusicMeasure() //part.measures[0]
		    let ticks = 0
		    let measureNotes = []
		    measure.staves = [measureNotes]

		    for (let note of notes) {
		    	measureNotes.push(note)
		    	ticks += note.duration
		    	if (ticks>=measureDuration) {
		    		part.measures.push(measure)

		    		measureNotes = []
		    		measure = new MusicMeasure()
		    		measure.staves = [measureNotes]	    		
		    		ticks = 0
		    	}
		    }
		    if (measureNotes.length>0) {
		    	part.measures.push(measure)
		    }
		}

		if (this.config && this.config.autoBeam) {
			for (let m = 0; m < part.measures.length; m++) {
		    	let prevBeats = this.prevBeats(part, m)
		    	let measure = part.measures[m]
		    	measure.autoBeam(prevBeats.beats, prevBeats.beatUnit)
		    }
		}
	}

	getPartNotes(part) {
		if (!part) {
			part = this.parts[0]
		}
		return part.getPartNotes()
	}
	gotoCursor(noteIndex) {
		let part = this.parts[this.cursor.partIndex]
		let count = 0
		for (let i = 0; i < part.measures.length; i++) {
			let measure = part.measures[i]
			let notes = measure.staves[this.cursor.staffIndex]
			if (noteIndex>=count && noteIndex<count+notes.length) {
				this.cursor.measureIndex=i
				this.cursor.noteIndex = noteIndex - count
				this.cursor.elemIndex = 0
				this.cursor.showCursor = true
				break
			}
			count += notes.length
		}
	}
	takeAction(action) {
		let part = this.parts[this.cursor.partIndex]
		switch (action.type) {
			case 'setClef':
				part.changeClef(this.cursor.measureIndex, this.cursor.staffIndex, this.cursor.noteIndex)
				return {score: this}
			case 'setBeats':
				this.changeBeats(this.cursor.measureIndex)
				return {score: this}
			case 'inputDots':
				this.inputDots()
    			return {score: this}
    		case 'setFifths':
    			this.changeFifths(this.cursor.measureIndex, action.offset)
    			return {score: this}
    		case 'delNote':
			    this.delCurNote(part)
    			return {
    				score: this, 
    				// measureIndex: res.measureIndex,
				    // noteIndex: res.noteIndex,
				    // append: false
				  }
			case 'insertBarline':
			    this.insertBarline(part, this.cursor)
    			return {score: this}
    		case 'inputNoteTie':
			    part.insertNoteTie(this.cursor)
    			return {score: this}
    		case 'inputNoteTuplet':
			    this.insertNoteTuplet(part)
    			return {score: this}
     		case 'changeBeam':
    			const measure = part.measures[this.cursor.measureIndex]
    			measure.changeBeam(this.cursor)
    			return {score: this}
    		case 'moveCursor':
	    		this.moveCursor(action.offset)
    			return {score: this}
    		case 'moveChordLevel': 
			    var note = part.measures[this.cursor.measureIndex].staves[this.cursor.staffIndex][this.cursor.noteIndex]
			    var elemIndex = this.cursor.elemIndex
				if (note && note.elems && elemIndex<note.elems.length) {
					var elem = note.elems[elemIndex]
					elem.line += action.offset
					if (action.offset>0 && elemIndex<note.elems.length-1 && elem.line>note.elems[elemIndex+1].line) {
						elemIndex += 1;
					}else if (action.offset<0 && elemIndex>0 && elem.line<note.elems[elemIndex-1].line) {
						elemIndex -= 1
					}
    				return {score: this, elemIndex: elemIndex}
				}
				break
			case 'addPart':
				let newPart = new MusicPart()
				this.parts.push(newPart)

				this.cursor.partIndex = this.parts.length-1
				this.cursor.staffIndex = 0
				this.cursor.measureIndex = 0
				this.cursor.noteIndex = 0
				this.cursor.append = true
				this.cursor.showCursor = true
    			return {
    				score: this,
				}
			case 'delPart':
				this.parts.splice(this.cursor.partIndex, 1)
    			return this.takeAction({type: 'movePartCursor', offset: -1})
			case 'movePartCursor':
				var staffIndex = this.cursor.staffIndex+action.offset
				var partIndex = this.cursor.partIndex
				if (staffIndex<0 || staffIndex>=part.staves) {
					partIndex += action.offset
					if (partIndex<0 || partIndex>=this.parts.length) {
						return
					}
					if (action.offset>0) {
						staffIndex = 0
					}else{
						staffIndex = this.parts[partIndex].staves-1
					}
				}
				if (partIndex>=0 && partIndex<this.parts.length) {
					let part = this.parts[partIndex]
					var measureIndex = this.cursor.measureIndex
					if(measureIndex>=part.measures.length) {
						measureIndex = part.measures.length-1
					}
					this.cursor.partIndex = partIndex
					this.cursor.staffIndex = staffIndex
					this.cursor.measureIndex = measureIndex
					this.cursor.noteIndex = 0
					this.cursor.append = false
					this.cursor.showCursor = true
					return {
	    				score: this, 
					}
				}
				break
			case 'changePartStaves':
				var staves = part.staves+action.offset
				if (staves>0 && staves<5) {
					part.staves = staves
					return {
						score: this,
					}
				}
				break
		    default:
				break
		}
	}
	setPartInstrument(name) {
		let part = this.parts[this.cursor.partIndex]
		part.name = name
	}
	get curStaves() {
		let staves = this.parts[this.cursor.partIndex].staves
		return staves
	}
	onkeydown(e) {
		let SELF = this
		let key = e.key
		if (key === 'ArrowUp') {
			SELF.takeAction({type: 'moveChordLevel', offset: 1})
		}else if (key === 'ArrowDown') {
			SELF.takeAction({type: 'moveChordLevel', offset: -1})
		}else if (key === 'ArrowLeft') {
			SELF.takeAction({type: 'moveCursor', offset: -1})
		}else if (key === 'ArrowRight') {
			SELF.takeAction({type: 'moveCursor', offset: 1})
		}else if (key === 'Backspace') {
			SELF.takeAction({type: 'delNote'})
		}else if (key === '=') {
			this.setNoteAccidental(Accidental.sharp)
		}else if (key === '-') {
			this.setNoteAccidental(Accidental.flat)
		}else if (key === 'n') {
			this.setNoteAccidental(Accidental.natural)
		}else if (key === 'r') {
			this.changeNoteRest()
		}else {
			let steps = ['c', 'd', 'e', 'f', 'g', 'a', 'b']
			let index = steps.indexOf(key)
			if (index>=0) {
				let elems = this.createElemsFromSteps([index])
				let note = {note: this.cursor.appendNote.note, elems: elems}
				this.addNote(note)
			}else{
				return true
			}
		}

		return false
	}
	loadFrom(json) {
		this.title = json.title;
		this.subtitle = json.subtitle;
		let parts = json.parts;
		for (let partJson of parts) {
			let part = new MusicPart(partJson)
			this.parts.push(part)
		}
	}
	getJson() {
		var json = {};
		json.title = this.title;
		json.subtitle = this.subtitle;
		json.parts = []
		for (var i = 0; i < this.parts.length; i++) {
			json.parts.push(this.parts[i].getJson())
		}
		return json
	}
	createElemsFromSteps(steps) {
		let elems = []
	    let partIndex=(this.cursor && this.cursor.partIndex) ? this.cursor.partIndex : 0
	    let staffIndex=(this.cursor && this.cursor.staffIndex) ? this.cursor.staffIndex : 0

		let part = this.parts[partIndex]
		let delta=1
	    if (part.clefs[staffIndex] === 'treble') {
	      delta += 12
	    }
	    let stepDelta = this.stepDelta ? this.stepDelta : 7
	    let firstStep = steps[0]
	    if (this.lastStep+1) {
	    	if (firstStep>this.lastStep+3) {
	    		stepDelta -= 7
	    	}else if (firstStep<this.lastStep-3) {
	    		stepDelta += 7
	    	}
	    }
	    this.lastStep = firstStep
	    this.stepDelta = stepDelta

		for(let step of steps) {
			elems.push({line: step+stepDelta-delta})
		}
		return elems
	}
	createNote(keys) {
		const stepOfKey = [0,0,1,1,2,3,3,4,4,5,5,6]
		var elems = []
	    var delta=1
	    let partIndex=(this.cursor && this.cursor.partIndex) ? this.cursor.partIndex : 0
	    let staffIndex=(this.cursor && this.cursor.staffIndex) ? this.cursor.staffIndex : 0

		let part = this.parts[partIndex]
	    if (part.clefs[staffIndex] === 'treble') {
	      delta += 12
	    }
	    for (var i = 0; i < keys.length; i++) {
	      const key = keys[i]
	      const step = (Math.floor(key/12)-4)*7+stepOfKey[key%12]
	      if (this.cursor.appendNote.rest) {
	        elems.push({line: 0})
	      }else{
	        elems.push({line: step-delta})
	      }
	    }
	    return {note: this.cursor.appendNote.note, rest: this.cursor.appendNote.rest, elems:elems}
	}
	addNote(note) {
		let noteCursor = this.cursor
		let part = this.parts[noteCursor.partIndex]
	    let measure = part.measures[noteCursor.measureIndex]
	    let m=noteCursor.measureIndex
	    let n=noteCursor.noteIndex;
	    let notes = measure.staves[noteCursor.staffIndex]
	    if (!noteCursor.append && n<notes.length) {
	    	notes[n] = note
	    }else{
	    	if (n>=notes.length-1) {
	    		let prevBeats = this.prevBeats(this.parts[0], m)
	    		let measureDuration = prevBeats.beats*480*4/prevBeats.beatUnit
	    		if (measure.measureTicks(prevBeats.beats, prevBeats.beatUnit)+MusicNote.noteDuration(note)>measureDuration) {
	    			if (m<part.measures.length-1) {
		    			m += 1
		    			n = -1
		    			measure = part.measures[m]
					}else{
				    	measure = new MusicMeasure()
				    	m=part.measures.push(measure)-1
				    	n=0
					}
					notes = measure.staves[noteCursor.staffIndex]
			    }
			}
	    	if (n<notes.length-1) {
	    		notes.splice(n+1, 1, note)
	    		n += 1
	    	}else {
	    		n=notes.push(note)-1
	    	}
	    }
	    this.cursor.measureIndex = m
	    this.cursor.noteIndex = n
	    this.cursor.append = true
	    this.cursor.showCursor = true
	    // return {measureIndex:m, noteIndex:n}

	    if (this.config && this.config.autoBeam) {
	    	let prevBeats = this.prevBeats(part, m)
	    	measure.autoBeam(prevBeats.beats, prevBeats.beatUnit)
	    }
	}
	inputDots() {
		let part = this.parts[0]
	    let n = part.measures[this.cursor.measureIndex].staves[this.cursor.staffIndex][this.cursor.noteIndex]
		n.dots = ((n.dots ? n.dots : 0) + 1)%3
		if (this.config && this.config.autoBeam) {
	    	let prevBeats = this.prevBeats(part, this.cursor.measureIndex)
	    	let measure = part.measures[this.cursor.measureIndex]
	    	measure.autoBeam(prevBeats.beats, prevBeats.beatUnit)
		}
	}
	delNoteElem() {
		let part = this.parts[this.cursor.partIndex]
		let measure = part.measures[this.cursor.measureIndex]
		let notes = measure.staves[this.cursor.staffIndex]
		let note = notes[this.cursor.noteIndex]
		if (note.elems && note.elems.length>1) {
			note.elems.splice(this.cursor.elemIndex, 1)
			this.cursor.elemIndex=0
		}else{
			this.cursor.elemIndex=0
			notes.splice(this.cursor.noteIndex, 1)
			if (this.cursor.noteIndex>0) {
				this.cursor.noteIndex -= 1
			}else{
				if (measure.isEmpty() && part.measures.length>1) {
					part.measures.splice(this.cursor.measureIndex, 1)
				}
				if (this.cursor.measureIndex>0) {
					this.cursor.measureIndex -= 1
					this.cursor.noteIndex = part.measures[this.cursor.measureIndex].staves[this.cursor.staffIndex].length-1
				}
			}
		}
	}
	delCurNote(part) {
		const measure = part.measures[this.cursor.measureIndex]
		if (!measure) {
			return
		}
		if (this.cursor.noteIndex===0 && measure.isEmpty()) {
			if (part.measures.length>1) {
				part.measures.splice(this.cursor.measureIndex,1)
			}
		}else{
			measure.delNote(this.cursor)
		}

		if (this.cursor.noteIndex>0) {
			this.cursor.noteIndex -= 1
		}else if (this.cursor.measureIndex>0) {
			this.cursor.measureIndex -= 1
			this.cursor.noteIndex = part.measures[this.cursor.measureIndex].staves[this.cursor.staffIndex].length-1
		}else{
			this.cursor.measureIndex = 0
			this.cursor.noteIndex = 0
		}
		this.cursor.append = false
		//return {measureIndex: notePos.measureIndex, noteIndex: notePos.noteIndex}
	}
	moveCursor(offset) {
		var measureIndex = this.cursor.measureIndex;
		var noteIndex = this.cursor.noteIndex+offset;
		var append = false;
		if (offset<0 && this.cursor.append) {
		  append = false;
		  noteIndex = this.cursor.noteIndex;
		}
		const part = this.parts[this.cursor.partIndex]
		if (noteIndex<0) {
		  if (this.cursor.measureIndex>0) {
		    measureIndex -= 1;
		    noteIndex = part.measures[measureIndex].staves[this.cursor.staffIndex].length-1;
		  }else{
		    noteIndex = this.cursor.noteIndex;
		    append = true;
		  }
		}else {
		  const noteCount = part.measures[measureIndex].staves[this.cursor.staffIndex].length;
		  if (noteIndex>=noteCount) {
		    if(!this.cursor.append) {
		      noteIndex = this.cursor.noteIndex
		      append = true
		    }else if (measureIndex<part.measures.length-1) {
		      measureIndex += 1;
		      noteIndex = 0;
		    }else {
		      part.appendMeasure()
		      measureIndex += 1
		      noteIndex = 0
		      append = false
		    }
		  }
		}
		this.cursor.measureIndex = measureIndex
		this.cursor.noteIndex = noteIndex
		this.cursor.elemIndex = 0
		this.cursor.append = append
		this.cursor.showCursor = true
	}
	moveMeasureCursor(offset) {
		var measureIndex = this.cursor.measureIndex+offset;
		var part = this.parts[this.cursor.partIndex];
		if (measureIndex<0 || measureIndex>part.measures.length-1) {
			return false;
		}
		const noteIndex = offset>0 ? 0 : part.measures[measureIndex].staves[this.cursor.staffIndex].length-1
		if (measureIndex<part.measures.length) {
			this.cursor.measureIndex = measureIndex
			this.cursor.noteIndex = noteIndex
			this.cursor.elemIndex = 0
			return true
		}
		return false
	}
	moveChordCursor(offset) {
		const measureIndex = this.cursor.measureIndex;
		const noteIndex = this.cursor.noteIndex;
		const part = this.parts[this.cursor.partIndex]
		const note = part.measures[measureIndex].staves[this.cursor.staffIndex][noteIndex];
		var elemIndex
		if (offset) {
			elemIndex = this.cursor.elemIndex+offset;
			if (elemIndex>=0 && elemIndex<note.elems.length) {
				this.cursor.elemIndex = elemIndex
				this.cursor.append = false
				return true
			}
		}else{
			elemIndex = (this.cursor.elemIndex+1)%note.elems.length
			if (note.elems[elemIndex].readonly) {
				elemIndex = (elemIndex+1)%note.elems.length
			}
			this.cursor.elemIndex = elemIndex
			this.cursor.append = false
			return true
		}
		return false
	}
	addNoteElem(elem) {
		let part = this.parts[this.cursor.partIndex]
		let measure = part.measures[this.cursor.measureIndex]
		let n = measure.staves[this.cursor.staffIndex][this.cursor.noteIndex]
		if (n.elems) {
			n.elems.push(elem)
			this.cursor.elemIndex = n.elems.length-1
			this.cursor.showCursor = true
		}
	}
	setNoteAccidental(accidental) {
		let part = this.parts[this.cursor.partIndex]
		var measure = part.measures[this.cursor.measureIndex]
		var n = measure.staves[this.cursor.staffIndex][this.cursor.noteIndex]
		if (n.elems) {
		  let elem = n.elems[this.cursor.elemIndex]
		  if (accidental) {
		    if (accidental === Accidental.sharp && elem.accidental === Accidental.sharp) {
		      elem.accidental = Accidental.doubleSharp
		      elem.alter = 2
		    }else if (accidental === Accidental.flat && elem.accidental === Accidental.flat) {
		        elem.accidental = Accidental.doubleFlat
			    elem.alter = -2
		    }else if ((accidental === Accidental.sharp && elem.accidental === Accidental.doubleSharp) || (accidental === Accidental.flat && elem.accidental === Accidental.doubleFlat) || (elem.accidental===accidental)) {
		    	elem.accidental = ''
		    	elem.alter = 0
		    }else{
		    	elem.accidental=accidental
		    	if (accidental===Accidental.flat) {
			    	elem.alter=-1
		    	}else if (accidental===Accidental.sharp) {
			    	elem.alter=1
		    	}else{
		    		elem.alter=0
		    	}
		    }
		  }
		}
	}
	changeNoteRest(rest) {
		let part = this.parts[this.cursor.partIndex]
		var measure = part.measures[this.cursor.measureIndex]
		var n = measure.staves[this.cursor.staffIndex][this.cursor.noteIndex]
		if (!n) {
			return
		}
		if (rest===true || rest===false) {
			n.rest = rest
		}else{
			n.rest = n.rest ? false : true
		}
	}
	changeNote(note, rest) {
		let part = this.parts[this.cursor.partIndex]
		var measure = part.measures[this.cursor.measureIndex]
		var n = measure.staves[this.cursor.staffIndex][this.cursor.noteIndex]
		if (!n) {
			this.cursor.append = true
		}
		if (note) {
		  if (!this.cursor.append) {
		    n.note=note
		  }
		  this.cursor.appendNote.note = note
		}
		if (!this.cursor.append) {
		  n.rest=rest
		}
	}
	prevFifths(part, measureIndex) {
		if (measureIndex>0) {
			for (var i = measureIndex; i >= 0; i--) {
				var measure = part.measures[i]
				if (measure.fifths !== null) {
					return measure.fifths
				}
			}
		}
		return part.fifths
	}
	changeFifths(measureIndex, offset) {
		let fifths = this.prevFifths(this.parts[0], measureIndex) + offset
	    if (fifths>7) {
	      fifths = 7
	    }else if (fifths<-7){
	      fifths =-7
	    }
	    for (var i = 0; i < this.parts.length; i++) {
	    	let part = this.parts[i]
	    	if (measureIndex===0) {
		    	part.fifths = fifths
	    	}else{
	    		var measure = part.measures[measureIndex]
				if (measure) {
					measure.fifths = fifths
				}
	    	}
	    }
	}
	prevBeats(part, measureIndex) {
		if (measureIndex>0) {
			for (var i = measureIndex; i >= 0; i--) {
				var measure = part.measures[i]
				if (measure.beats) {
					return {beats: measure.beats, beatUnit: measure.beatUnit}
				}
			}
		}
		return {beats: part.beats, beatUnit: part.beatUnit}
	}
	changeBeats(measureIndex) {
		const beatTypes = [{beat:2, unit:4}, {beat:3, unit:4}, {beat:4, unit: 4}, {beat:3, unit:8}, {beat:6, unit:8}, {beat:0, unit:0}]
		var nextTypeIndex = 1;
		var prevBeats = this.prevBeats(this.parts[0], measureIndex)

		for (var i = 0; i < beatTypes.length; i++) {
			const type = beatTypes[i]
			if (type.beat===prevBeats.beats && type.unit===prevBeats.beatUnit) {
				nextTypeIndex = (i+1)
				if (nextTypeIndex>beatTypes.length-1) {
					nextTypeIndex = 0
				}
				break;
			}
		}
		const nextType = beatTypes[nextTypeIndex]
		for (var j = 0; j < this.parts.length; j++) {
			let part = this.parts[j]
			if (measureIndex===0) {
				part.beats = nextType.beat
				part.beatUnit=nextType.unit
			}else{
				var measure = part.measures[measureIndex]
				if (measure) {
					if (nextType.beat===0) {
						measure.beats = null
						measure.beatUnit=null
					}else{
						measure.beats = nextType.beat
						measure.beatUnit=nextType.unit
					}
				}
			}
		}
	}
	insertNoteTuplet(part) {
		part.insertNoteTuplet(this.cursor)
		let measure = part.measures[this.cursor.measureIndex]
		let notes = measure.staves[0]
		MusicNote.calcNoteDuration(notes)
	}
	
	insertBarline(part, notePos) {
		const measure = part.measures[notePos.measureIndex]
		var barline = measure.barline
		var notes = measure.staves[notePos.staffIndex]
		if (notePos.noteIndex>=notes.length-1) {
			if (barline==null || barline==='|') {
				barline = ':|'
			}else if (barline === ':|') {
				barline = '|:'
			}else if (barline==='|:') {
				barline = '.|'
			}else {
				barline = null
				if (notePos.measureIndex<part.measures.length-1) {
					const nextMeasure = part.measures[notePos.measureIndex+1]
					notes = notes.concat(nextMeasure.staves[notePos.staffIndex])
					part.measures.splice(notePos.measureIndex+1, 1)
				}
			}
		}else{
			measure.removeBeams(notePos.staffIndex, notePos.noteIndex)
			var m = new MusicMeasure()
			m.staves[notePos.staffIndex] = notes.splice(notePos.noteIndex+1, notes.length-notePos.noteIndex-1)
			part.measures.splice(notePos.measureIndex+1, 0, m)
		}
		for (var i = 0; i < this.parts.length; i++) {
			let part = this.parts[i]
			if (notePos.measureIndex<part.measures.length) {
				let measure = part.measures[notePos.measureIndex]
				measure.barline = barline
			}
		}
	}

	calcMeasurePosition() {
		this.max_measure = 0
		for (let part of this.parts) {
			if (this.max_measure<part.measures.length) {
				this.max_measure = part.measures.length
			}
		}
		// console.log('calcMeasurePosition:'+this.max_measure)
		for (var m = 0; m < this.max_measure; m++) {
			var measure_width = 0
			for (var i = 0; i < this.parts.length; i++) {
			    let part = this.parts[i]
			    if (m>=part.measures.length) {
			    	continue
			    }
			    let measure = part.measures[m]
				measure.calcNoteOffsetx()
				//console.log(measure)
				if (measure_width<measure.width) {
					measure_width = measure.width
				}
				measure.resetTieType()
				// measure.rearrangeMeasure(this.beats, this.beatUnit)
				measure.calcElemLines(this.beatUnit)
		    }
		    for (var j = 0; j < this.parts.length; j++) {
			    let part = this.parts[j]
			    let measure = part.measures[m]
			    if (measure) {
				    measure.width = measure_width
			    }
		    }
		}
	}
	getItems(startx, basey) {
		var items = []
	    var x=startx+lineh;

	    this.calcMeasurePosition()
	    const res = this.parts[0].getStartMeasureIndex(this.cursor.measureIndex, this.cursor.noteIndex)
	    const startm = res.startm
	    const stopm = res.stopm+1

	    if (this.title) {
		    items.push(<Glyph key={items.length} type='text' params={{text:this.title, x: x-1*lineh}} fontSize={lineh*1.6} basey={basey-5*lineh} />)
	    }
	    if (this.subtitle) {
		    items.push(<Glyph key={items.length} type='text' params={{text:this.subtitle, x: x-1*lineh}} fontSize={lineh*1.1} basey={basey-3*lineh} />)
	    }
	    for (var i = 0; i < this.parts.length; i++) {
		    let part = this.parts[i]
		    if (i===0 && this.max_measure>3) {
		    	items.push(<Glyph key={items.length} type='text' params={{text:startm+1, x: x-lineh}} fontSize={lineh*1.2} basey={basey} />)
		    }
		    part.addItems(items, x, basey, startm, stopm, this.cursor.showCursor && this.cursor.partIndex===i ? this.cursor : null)
		    basey += systemDistance+4*lineh
		    if (part.staves>1) {
		    	basey += (part.staves-1)*(staffDistance+4*lineh)
		    }
	    }

	    return items
	  }
}

export default MusicScore;



