import React from 'react';
import {lineh, systemDistance, staffDistance, systemMarginLeft} from '../config/MusicConfig';
import {Fiveline, Line, Bracket} from './MusicGlyphFont';

/*
踏板作为shift键，踩下工具条改变功能。
加个显示各种表情记号。
中间可以变谱号、调号、拍号。
1234
*/

class Staff extends React.Component {
  getFivelines(left, top, partIndex, staffIndex) {
    let fiveLines = []
    var y = top //? this.props.top : -4.92*lineh
    var key = 0
    for (var lineIndex = 0; lineIndex < this.props.score.parts.length; lineIndex++) {
      var part = this.props.score.parts[lineIndex]
      if (part.staves>1) {
        let height = staffDistance*(part.staves-1)+4*lineh*part.staves
        fiveLines.push(<Bracket key={key} x={left-lineh} y={y+3.92*lineh} height={height}/>)
        key += 1
      }
      for (var i = 0; i < part.staves; i++) {
        if (lineIndex === partIndex && i===staffIndex) {
          fiveLines.push(<Line key={key} left={left-3} top={y+3.8*lineh} width={6} height={4.1*lineh} color='red'/>)
          key += 1
        }

        fiveLines.push(<Fiveline key={key} top={y} left={left}/>)
        y += staffDistance+4*lineh
        key += 1
      }
      y += systemDistance-staffDistance
    }
    var y0 = top+3.92*lineh
    y = y-systemDistance+staffDistance-3*lineh-y0
    fiveLines.push(<Line key={key} left={left} top={y0} width={2} height={y}/>)
    return fiveLines
  }
  render() {
    let top = this.props.top ? this.props.top : 0
    let left = this.props.left ? this.props.left : 0
    left += systemMarginLeft
    const items = this.props.score.getItems(left, top);
    let fiveLines = (!this.props.score.config || !this.props.score.config.hideFiveline) ? this.getFivelines(left, top, this.props.score.cursor.partIndex, this.props.score.cursor.staffIndex) : null

    return (
      <div>
        {fiveLines}
        {items}
      </div>
      )
  }
}
export default Staff;



