import React from 'react';
import {lineh, staffDistance} from '../config/MusicConfig';
import GlyphNote, {accidentalElems} from './MusicGlyphNote';
import Musfont, {Line} from './MusicGlyphFont';
import {Accidental} from '../music/MusicTypes';

//number
const numNotes = ['\u0130', '\u0131', '\u0132', '\u0133', '\u0134', '\u0135', '\u0136', '\u0137', '\u0138', '\u0139'];
//clef
const clefTreble = '\u0180';
const clefBass = '\u0181';
const clefMid = '\u0182';
// barline
// const barline = '\u01bb';
// const barlineBegin = '\u01bd';
// const barlineEnd = '\u01be';
const barlineDot = '\u01bf';
// const restNotes = {1:'\u0161',2:'\u0162',4:'\u0163',8:'\u0164',16:'\u0165',32:'\u0166',64:'\u0167',128:'\u0168'};
// const artDot = '\u01c7'

//调号位置
const sharpTrebleLines = [4, 1,  5, 2, -1, 3,  0]
const flatTrebleLines =  [0, 3, -1, 2, -2, 1, -3]
const sharpBassLines = [ 2, -1,  3, 0, -3,  1, -2]
const flatBassLines =  [-1,  1, -3, 0, -4, -1, -5]

export class GlyphFifths extends React.Component {
  render() {
    const fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
    const fifths = this.props.fifths;
    const total = Math.abs(fifths);
    const text =  fifths>0 ? accidentalElems[Accidental.sharp] : accidentalElems[Accidental.flat]
    var lines
    if (this.props.clef === 'treble') {
      lines = (fifths>0) ? sharpTrebleLines : flatTrebleLines
    }else{
      lines = (fifths>0) ? sharpBassLines : flatBassLines
    }

    let lineHeight = lineh
    if (this.props.lineh) {
      lineHeight = this.props.lineh
    }
    var items=[]
    for (var i = 0; i < total; i++) {
      const top = -lines[i]*lineHeight/2
      items.push(<Musfont key={i} left={i*lineHeight+1} top={top} glyph={text} fontSize={fontSize} />)
    }
    // console.log('GlyphFifths:'+this.props.left+' '+this.props.basey+' fifths:'+fifths)
    return (
        <div style={{position: 'absolute', left: this.props.left, top: this.props.basey}}>
        {items}
        </div>
      )
  }
}

class GlyphBarline extends React.Component {
  render() {
    const fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
    var height = (this.props.params.staves-1)*staffDistance+4*lineh*this.props.params.staves
    var width = lineh/8
    if (width<1) {width=1}
    var dots = null
    if (this.props.barline === ':|' || this.props.barline === '|:') {
      var dotLeft = (this.props.barline === ':|') ? -8*width : 6*width

      if (this.props.params.staves>1) {
        dots = []
        for (var i = 0; i < this.props.params.staves; i++) {
          dots.push(<Musfont key={i} left={dotLeft} top={-2*lineh+i*(staffDistance+4*lineh)} glyph={barlineDot} fontSize={fontSize} />)
        }
      }else{
        dots = <Musfont left={dotLeft} top={-2*lineh} glyph={barlineDot} fontSize={fontSize} />
      }
    }
    if (this.props.barline === ':|' || this.props.barline === '.|') {
      return(
        <div style={{position: 'absolute', left: this.props.left, top: this.props.top+1.92*lineh}}>
          {dots}
          <Line left={-2*width} top={0} width={width} height={height}/>
          <Line left={width} top={0} width={width*5} height={height}/>
        </div>
      )
    }else if (this.props.barline === '|:') {
      return(
        <div style={{position: 'absolute', left: this.props.left, top: this.props.top+1.92*lineh}}>
          {dots}
          <Line left={-4*width} top={0} width={width*5} height={height}/>
          <Line left={3*width} top={0} width={width} height={height}/>
        </div>
      )
    }else{
      return(
        <Line left={this.props.left} top={this.props.top+1.92*lineh} width={width} height={height}/>
      )
      // return(<Musfont left={this.props.left} top={this.props.top} glyph={this.props.barline === '.|' ? barlineEnd : barline} fontSize={fontSize} />)
    }
  }
}

class Glyph extends React.Component {
  render() {
    // const basey = this.props.basey ? this.props.basey-lineh*2.85 : 0;
    const basey = this.props.basey ? this.props.basey : 0;
    var top=basey
    const line = this.props.params.line;
    if (line) {
      top -= line*lineh/2;
    }
    var fontSize = this.props.fontSize ? this.props.fontSize : 2*lineh;
    const type = this.props.type ? this.props.type : 'note';
    const value = this.props.params.value;
    var text;
    var color = this.props.color ? this.props.color : this.props.params.color ? this.props.params.color : null
    if (type === 'beats' || type === 'beatUnit') {
      text = numNotes[value];
    }else if (type==='note') {
      return (<GlyphNote left={this.props.left} basey={basey} params={this.props.params} color={this.props.color} fontSize={fontSize} />)
    }else if (type==='barline') {
      return (<GlyphBarline barline={value} left={this.props.left} top={top} fontSize={fontSize} params={this.props.params}/>)
      // text = value==='.|' ? barlineEnd : barline;
    }else if (type==='clef') {
      text = value==='treble' ? clefTreble : value==='bass' ? clefBass : clefMid;
      console.log(value)
      if (this.props.scale) {
        fontSize *= this.props.scale
        if (value==='treble') {
          top += 3.4*lineh
        }else if (value==='soprano') { //1线是C
          top += 2.35*lineh
        }else if (value==='mezzosoprano') { //2线是C
          top += 1.35*lineh
        }else if (value==='alto') { //3线是C
          top += 0.35*lineh
        }else if (value==='tenor') { //4线是C
          top += -0.35*lineh
        }else if (value==='baritone') { //5线是C
          top += -1.35*lineh
        }else{
          top += 1.35*lineh
        }
      }else{
        if (value==='soprano') { //1线是C
          top += 1*lineh
        }else if (value==='mezzosoprano') { //2线是C
          top += 0*lineh
        }else if (value==='alto') { //3线是C
          top += -1*lineh
        }else if (value==='tenor') { //4线是C
          top += -2*lineh
        }else if (value==='baritone') { //5线是C
          top += -3*lineh
        }
      }

    }else if (type==='fifths') {
      return (<GlyphFifths left={this.props.left} basey={basey} clef={this.props.params.clef} fifths={this.props.params.fifths} fontSize={fontSize}/>)
    }else if (type==='text') {
      const styleText = {position: 'absolute', left: this.props.params.x, top: top+lineh*2, color: 'black', fontSize: fontSize}
      return <div style={styleText}>{this.props.params.text}</div>
    }else if (type==='rect') {
      return (<div style={{position: 'absolute', left:this.props.left, top: top, width: this.props.params.width, height: this.props.params.height, background: color}} />)
    }
    return (<Musfont left={this.props.left} top={top} glyph={text} color={color} fontSize={fontSize} />)
  }
}

export default Glyph;



