
const textOfStep=['C','D','E','F','G','A','B']
class QNoteElem {
	constructor() {
		this.step = 0 //0...6
		this.octave = 4
		this.alter = 0
		this.readonly = false

		//计算出的
		this.line = 0
		this.value = 0
	}
	clone() {
		let res = new QNoteElem()
		for (let key in this) {
			res[key] = this[key]
		}
		return res
	}
	setNoteValue() {
		//note value
		this.value = QNoteElem.valueFromStep(this.step, this.alter, this.octave)
	}
	add(deltaStep, yinshu) {
		var res = new QNoteElem() //{alter: 0}
		res.octave = this.octave
		res.step = this.step+deltaStep
		if (res.step>6) {
			res.step = res.step%7
			res.octave += 1
		}
		res.setNoteValue()
		var newValue = this.value+yinshu
		res.alter = newValue-res.value
		res.value = newValue
		return res
	}
	/*
	*/
	//根据clef, note.step, note.octave, note.alter计算note.accidental
	setNoteValueLineAcc(clef = 'treble') {
		this.setNoteValue()
		//note line
		var line = this.step
		if (clef === 'bass') {
			line -= 1
			line += (this.octave-4)*7
		}else if (clef === 'soprano') { //1线是C
			line -= 4
			line += (this.octave-5)*7
		}else if (clef === 'mezzosoprano') { //2线是C
			line -= 2
			line += (this.octave-5)*7
		}else if (clef === 'alto') { //3线是C
			line -= 0
			line += (this.octave-5)*7
		}else if (clef === 'tenor') { //4线是C
			line -= 5
			line += (this.octave-4)*7
		}else if (clef === 'baritone') { //5线是C
			line -= 3
			line += (this.octave-4)*7
		}else{ //treble
			line -= 6
			line += (this.octave-5)*7
		}
		this.line = line

		//note accidental
		if (this.alter === 1) {
			this.accidental = 'sharp'
		}else if (this.alter === 2) {
			this.accidental = 'sharp-sharp'
		}else if (this.alter === -1) {
			this.accidental = 'flat'
		}else if (this.alter === -2) {
			this.accidental = 'flat-flat'
		}else {
			this.accidental = null
		}
	}
	textOfStep() {
		return textOfStep[this.step]
	}
	textOfAlter() {
		if (this.alter === 1) {
			return '#'
		}else if (this.alter === 2) {
			return 'x'
		}else if (this.alter === -1) {
			return 'b'
		}else if (this.alter === -2) {
			return 'bb'
		}
		return ''
	}
	static valueFromStep(step, alter, octave) {
		var val = octave*12
		if (step<3) {
			val += 2*step
		}else{
			//step:3 -> value: 5
			//4 -> 7, 5->9 6->11
			val += 3+2*(step-2)
		}

		return val+(alter ? alter : 0)
	}
	static noteFromLine(line, alter, clef) {
		let yinhu = line+7*5
		if (clef === 'treble') {
			//line -6 中央C
			yinhu += 6
		}else if (clef === 'bass') {
			//line 6 中央C: 60
			yinhu += -6
		}else if (clef === 'soprano') {
			//1线是C
			//line -4 C
			yinhu += 4
		}else if (clef === 'mezzosoprano') {
			//2线是C
			//line -2 C
			yinhu += 2
		}else if (clef === 'alto') {
			//3线是C
			//line 0 C
			yinhu += 0
		}else if (clef === 'tenor') {
			//4线是C
			//line 2 C
			yinhu += -2
		}else if (clef === 'baritone') {
			//5线是C
			//line 4 C
			yinhu += -4
		}
		let note = new QNoteElem()
		note.octave = Math.floor(yinhu/7)
		note.step = yinhu%7
		note.alter = alter
		note.setNoteValueLineAcc(clef)
		return note
	}
	//生成min...max之间的随机整数
	static random(max, min=0) {
		return Math.floor((max-min+1)*Math.random()+min)
	}
	static randomItem(array) {
		return array[this.random(array.length-1)]
	}
	/*
	生成随机note 
	params:
		config {
			alter: -2 ... 2
			clef: treble or bass
			min: 'Xn' 其中X是CDEFGAB, n是1~7。C4是中央C(音值60)
			max: 同min
		}
	return:
		{step: Int, octave: 4, alter: -2...2, line: Int, value: Int}
	*/
	static stepOfXn(Xn) {
		let step = textOfStep.indexOf(Xn[0])
		let octave = parseInt(Xn[1], 10)+1
		return {step: step, octave: octave}
	}
	static noteValueOfXn(Xn) {
		let {step, octave} = this.stepOfXn(Xn)
		let value = this.valueFromStep(step, 0, octave)
		return value
	}
	static randomNoteWithConfig(config) {
		let min=4*7+0  //{step: 0, octave: 4}
		let max=5*7+0 //{step: 0, octave: 5}
		if (config.min) {
			let {step, octave} = this.stepOfXn(config.min)
			// let step = textOfStep.indexOf(config.min[0])
			// let octave = parseInt(config.min[1], 10)
			min = octave*6+step
		}
		if (config.max) {
			let {step, octave} = this.stepOfXn(config.max)
			// let step = textOfStep.indexOf(config.max[0])
			// let octave = parseInt(config.max[1], 10)
			max = octave*6+step
		}
		if (max<min) {
			[max, min] = [min, max]
		}
		let index = this.random(max, min)

		let res = new QNoteElem()
		res.octave = Math.floor(index/7)
		res.step = index%7
		res.alter = config.alter ? config.alter : 0
		res.setNoteValueLineAcc(config.clef ? config.clef : 'treble')

		return res
	}

	/*
	生成随机note 
	params:
		alter: null 随机升级号， -2 ... 2
		clef: treble or bass
	return:
		{step: Int, octave: 4, alter: -2...2, line: Int, value: Int}
	*/
	static randomNote(alter, clef='treble') {
		let line = this.random(6, -6)
		let curAlter = (alter===0 || alter) ? alter : this.random(1, -1)
		let note = QNoteElem.noteFromLine(line, curAlter, clef) 

		// var note = new QNoteElem()
		// note.step = this.random(6) //Math.floor(7*Math.random()) //0...6
		// note.alter = alter===0 || alter ? alter : this.random(1, -1) //Math.floor(3*Math.random()-1) //-1...1
		// if (clef==='treble' || clef==='soprano' || clef==='mezzosoprano' || clef==='alto') {
		// 	note.octave = 5
		// }else{
		// 	note.octave = 4
		// }
		// note.setNoteValueLineAcc(clef)
		return note
	}
}
export default QNoteElem;



