import React, { Component } from 'react';
import Toolbar from '../components/Toolbar';
import MusicSetting from './MusicSetting';
import MusicEditor from './MusicEditor';
import '../App.css';
import '../Home.css';

const homeIcons = {
	note: '\ue6c6',
	interval: '\ue6c4',
	chord: '\ue6c2',
	keysignature: '\ue6c3',
	scale: '\ue6c5',
	rhythm: '\ue6a1',
}

export class LeftTitle extends Component {
	render() {
		let width =  window.document.body.offsetWidth
		//let height = window.document.body.offsetHeight
		let border_background = 'linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,1) 50%, rgba(200,200,200,0))'
		let background = 'linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,0.5) 50%, rgba(200,200,200,0))'
		let leftDiv = 
			<div style={{display: 'table-cell', width: width/2+10, verticalAlign: 'middle'}}>
				<div style={{background: border_background, height: 2, margin: '0px 40px'}} />
				<p style={{fontSize:80, textAlign: 'center', background: background, padding: 80, margin: '0px 40px'}}>{this.props.title}</p>
				<div style={{background: border_background, height: 2, margin: '0px 80px'}} />
			</div>
		return leftDiv
	}
}

class Home extends Component {
	constructor(props) {
		super(props)
		// window.level = 1
		window.curComponent = this
		this.exercises = [
			{
				title: '识别练习 Identification',
				type: 'Identification',
				items: [
					['note','单音', 'Note'],
					['interval','音程', 'Interval'],
					['chord','和弦', 'Chord'],
					['keysignature','调号', 'Key Signature'],
					['scale','音阶', 'Scale'],
					['terms','音乐术语', 'Music Terms'],
				]
			},
			{
				title: '写作练习 Construction',
				type: 'Construction',
				items: [
					['note','单音', 'Note'],
					['interval','音程', 'Interval'],
					['chord','和弦', 'Chord'],
					// ['keysignature','调号 Key Signature'],
					['scale','音阶', 'Scale'],
				]
			},
			{
				title: '弹奏练习 Keyboard',
				type: 'Keyboard',
				items: [
					['note','单音', 'Note'],
					['interval','音程', 'Interval'],
					['chord','和弦', 'Chord'],
					['scale','音阶', 'Scale'],
					// ['rhythm','节奏 Rhythm'],
				]
			},
			{
				title: '听辩练习 Ear Training',
				type: 'EarTraining',
				items: [
					['note','单音', 'Note'],
					['interval','音程', 'Interval'],
					['chord','和弦', 'Chord'],
					['scale','音阶', 'Scale'],
					['rhythm','节奏', 'Rhythm'],
				]
			},
			{
				title: '其他工具',
				type: 'toolbox',
				items: [
					// ['exam','模拟测试'],
					['editor','创建曲谱'],
					['exam','综合测试'],
				]
			},
		]
		// console.log('start home')
		// console.log(window.stateHome)
		this.state = window.stateHome ? window.stateHome : {
			row: 0,
			column: 0,
		}
	}
	setState(state) {
		super.setState(state)
		window.stateHome = state(this.state)
	}
	takeAction(action) {
		let newState
		switch (action.type) {
		case 'moveColumn':
			let column = this.state.column + action.offset
			let row = this.state.row
			if (column<0) {
				if (row<=0) {
					return
				}else{
					row -= 1
					let exercise = this.exercises[row]
					column = exercise.items.length-1
				}
			}else{
				let exercise = this.exercises[row]
				if (column>=exercise.items.length) {
					if (row>=this.exercises.length-1) {
						return
					}else {
						row += 1
						column = 0
					}
				}
			}
			newState = {row: row, column: column}
			break
		case 'moveRow':
		{
			let row = this.state.row + action.offset
			if (row<0 || row>=this.exercises.length) {
				return
			}
			let column = this.state.column
			let exercise = this.exercises[row]
			if (column>=exercise.items.length) {
				column = exercise.items.length-1
			}
			newState = {row: row, column: column}
		}
			break
		case 'OK':
		{
			let exercise = this.exercises[this.state.row]
			let item = exercise.items[this.state.column]
			// window.location.href = this.linkOfExcise(exercise, item)
			window.theApp.pushWindow(this.linkOfExcise(exercise, item))
			return
		}
		default:
			break
		}
		this.setState(prevState => (newState))
	}
	onkeydown(e) {
		let SELF = window.curComponent
		let key = e.key
		if (key === 'ArrowUp') {
			SELF.takeAction({type: 'moveRow', offset: -1})
		}else if (key === 'ArrowDown') {
			SELF.takeAction({type: 'moveRow', offset: 1})
		}else if (key === 'ArrowLeft') {
			SELF.takeAction({type: 'moveColumn', offset: -1})
		}else if (key === 'ArrowRight') {
			SELF.takeAction({type: 'moveColumn', offset: 1})
		}else if (key === 'Enter') {
			SELF.takeAction({type: 'OK'})
		}else {
			return true
		}

		return false
	}
	linkOfExcise(exercise, item) {
		if (exercise.type === 'toolbox' && item[0] === 'editor') {
			return <MusicEditor />
			// if (item[0] === 'editor') {
			// 	return <MusicEditor />
			// }else{
			// 	return <MusicExam />
			// }
		}else{
			return <MusicSetting type={item[0]} qtype={exercise.type}/>
		}
	}
	render() {
		let cellWidth = window.device==='piano' ? 260 : window.device==='mobile' ? 120 : 170
		let cellHeight = window.device==='piano' ? 80 : window.device==='mobile' ? 50 : 60
		let elems = []
		for (var i = 0; i < this.exercises.length; i++) {
			let exercise = this.exercises[i]
			let values = exercise.items;
			let items = []
			for (let index=0; index<values.length; index++) {
			// let items = values.map((item, index) => {
				let item = values[index]
				let className = 'Home-icon'
				if(window.device !== 'mobile' && this.state.row===i && this.state.column===index) {
					className += ' Home-icon-hi'
				}
				let link = this.linkOfExcise(exercise, item)
				let iconStyle = {display: 'table-cell', fontSize:cellHeight/2, width: cellHeight/2, paddingLeft: cellWidth/8}
				if (window.device==='mobile') {
					iconStyle.lineHeight = '40px'
				}
				let icon = <div className='iconfont' style={iconStyle}>{homeIcons[item[0]]}</div>
				items.push(
					<div key={item[0]} onClick={(e) => {window.theApp.pushWindow(link)}} className={className} style={{width: cellWidth, height: cellHeight, marginLeft: cellWidth/11}}>
						{icon}
						<div style={{display: 'table-cell', width: cellWidth*0.6, height: cellHeight, lineHeight: 1,  verticalAlign: 'middle', textAlign: 'center'}}>
							{item[1]}<br /> <small> {item[2]} </small>
						</div>
					</div>
				)
			}
			elems.push(
				<div key={i} style={{display: 'table', margin: '42px 0px'}}>
					<li style={{fontSize: cellHeight*0.4}}>{exercise.title}</li>
					<div className='White-line' style={{width: cellWidth*4}}></div>
					{items}
				</div>
				)
		}
		let toolbar = null
		if (window.device!=='mobile') {
			toolbar = <Toolbar systemButtons={{arrow: true, ok: 'OK'}} action={(action) => this.takeAction(action)}/>
		}
		let leftDiv = null
		if (window.device === 'piano') {
			leftDiv = <LeftTitle title='Find 音乐学院基本乐科' />
		}
		
		return (
			<div style={{padding: 10, display: 'table-cell'}}>
				{leftDiv}
				<div style={{display: 'table-cell'}}>{elems}</div>
				{toolbar}
			</div>)
	}
}

export default Home;



