import QNoteElem from './QNote';
import QChord from './QChord';
import {Question, KeyboardType} from './BaseQuestions';

class QChordQuestions {
	/*
	1. 原位和转位的三和弦（纯白键）	
	2. 原位和转位的七和弦（纯白键）	
	3. 原位与转位的三和弦（带升，降，还原）	
	4. 原位与转位的七和弦（带重升，重降）	
	5. 九，十一，十三和弦	
	备注：目前国内与乐理考试最终只测验7和弦	
乐理考试中不会出现属和弦字样，所以在七和弦中，不出现属七和弦与增属和弦两个品种	
乐理考试中的属七和弦会被命名为大小七和弦	
	config={
		level: 1...5
		types: ['大三和弦', '小三和弦', '减三和弦', '增三和弦', '大七和弦', '小七和弦', '大小七和弦', '减七和弦', '半减七和弦', '增大七和弦', '小大七和弦']
		qtypes: [QTChooseQuality,QTWriteWithRootThirdFifth, QTWriteWithBottom]
		//steps: [3,7,9,11,13]
		inversions: ['转位','原位']
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (config.level) {
			config = {}
			if (config.level === 1) {
				config.steps = [3]
				config.alters = [0]
			}else if (config.level === 2) {
				config.steps = [7]
				config.alters = [0]
			}else if (config.level === 3) {
				config.steps = [3]
				config.alters = [-1, 0, 1]
			}else if (config.level === 4) {
				config.steps = [7]
				// config.alters = [-2, -1, 0, 1, 2]
			}else {
				config.steps = [9, 11, 13]
			}
		}
		if (config.difficulty) {
			if (config.difficulty[0].startsWith('1')) {
				config.alters = [0]
			}else if (config.difficulty[0].startsWith('2')) {
				config.alters = [-1, 0, 1]
			}else {
				config.alters = [-2, -1, 0, 1, 2]
			}
		}
		let inversions = []
		if (config.inversions) {
			if (config.inversions.includes('原位')) {
				inversions.push(0)
			}
			if (config.inversions.includes('转位')) {
				inversions.push(1,2)
			}
		}else{
			inversions = [0]
		}
		if (inversions.length>0) {
			config.inversions = inversions
		}
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.chordQuestion1(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			let writeType
			if (config.chordWriteTypes) {
				writeType = QNoteElem.randomItem(config.chordWriteTypes)//0: 低音，1根音，3， 三音， 5 五音， 7: 七音
			}else{
				writeType = QNoteElem.randomItem(['低音', '根音', '三音', '五音', '七音'])
			}
			this.chordQuestionConstruction(questions, config, writeType)
			// if (!config.qtypes ||  config.inversions.includes(0) ) {
			// 	this.chordQuestion2(questions, config)
			// }
			// if (!config.qtypes ||  config.inversions.includes(1)) {
			// 	this.chordQuestion3(questions, config)
			// }
		}
		if (!config.qtypes ||  config.qtypes.includes('Keyboard')) {
			this.chordQuestionKeyboard(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('EarTraining')) {
			config.ignoreUncommonNote = true
			this.chordQuestionEarTraining(questions, config)
		}
		return questions
	}
	// static copyNotes(notes, expectIndex) {
	// 	var elems = []
	// 	for (var k = 0; k < notes[0].elems.length; k++) {
	// 		if (k !== expectIndex) {
	// 			elems.push(notes[0].elems[k])
	// 		}
	// 	}
	// 	return [{note:notes[0].note, elems: elems}]
	// }
	static copyNotes(notes, expectNoteValue) {
		var elems = []
		for (let elem of notes[0].elems) {
			if (elem.value !== expectNoteValue) {
				elems.push(elem)
			}
		}
		return [{note:notes[0].note, elems: elems}]
	}
	static chordQuestion1(questions, config) {
		var chord = QChord.randomChord(config)
		if (!chord.name) {
			console.log('error: QChord.randomChord')
			console.log(chord)
		}
		// var choices = QChord.randomChordNames(config, 3, chord.name)
		// var answer = QNoteElem.random(choices.length)
		// choices.splice(answer, 0, chord.name)
		let choices = config.types
		let answers = [choices.indexOf(chord.type)]
		if (config.inversions.includes(1)) {
			if (chord.inversion) {
				answers.push(choices.length+chord.inversion-1)
			}
			choices.push('第一转位','第二转位','第三转位')
		}

		var question = new Question()
		question.desc = '判断和弦性质'
		question.score = {notes:chord.notes}
		question.choices = choices
		question.answers = answers
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
	static elemOfChordNote(chord, writeType) {
		let note = chord.notes[0]
		let elem = null
		if (writeType==='低音') {
			elem = note.elems[0]
		}else {
			for (let e of note.elems) {
				if ((writeType==='根音' && e.index === 0) ||
					(writeType==='三音' && e.index === 1) ||
					(writeType==='五音' && e.index === 2) ||
					(writeType==='七音' && e.index === 3)
					)
				 {
					elem = e
					break
				}
			}
		}
		return elem
	}
	static chordQuestionConstruction(questions, config, writeType) {
		// var inversions = config.inversions
		// config.inversions=[1,2]
		let chord = QChord.randomChord(config)
		if (chord) {
			let elem = this.elemOfChordNote(chord, writeType)
			if (!elem) {
				elem = chord.notes[0].elems[0]
				writeType = '低音'
			}
			elem.fill='grey'
			elem.readonly=true

			let question = new Question()
			question.desc = '以指定的音为'+writeType+'，构成和弦：'+chord.name
			question.score = {chordOnly: true, notes:[{note:8, elems:[elem]}]}
			// question.answers = {notes: this.copyNotes(chord.notes, elem.value)} //{notes: chord.notes}
			question.answers = {notes: chord.notes} //{notes: chord.notes}
			question.keyboard = KeyboardType.WriteNotes
			questions.push(question)
		}
		// config.inversions=inversions
	}
	static chordQuestionKeyboard(questions, config) {
		let chord = QChord.randomChord(config)
		if (chord) {
			let question = new Question()
			question.desc = '弹奏和弦: '+chord.english
			question.answers = {ignoreOctave: true, notes: chord.notes} //{notes: chord.notes}
			if (config.freeChord && config.freeChord.length>0) {
				question.answers.freeChord = true
			}
			question.keyboard = KeyboardType.PlayKeys
			questions.push(question)
		}
	}
	static chordQuestionEarTraining(questions, config) {
		let chord = QChord.randomChord(config)
		if (chord) {
			let choices = config.types

			var answers = [choices.indexOf(chord.name)]
			let events = []
			for (let note of chord.notes[0].elems) {
				events.push({tick: 0, note: note.value, duration: 480})
			}
			let question = new Question()
			question.answers = answers
			question.choices = choices
			question.desc = '选择弹奏的和弦'
			question.midi = {events:events}
			if (config.harmony.includes('柱式和弦')) {
				question.midi.harmony = true
			}
			question.keyboard = KeyboardType.Choice
			questions.push(question)
		}
	}
}

export default QChordQuestions;



