const majorMode = ['♭C','♭G','♭D','♭A','♭E','♭B','F','C','G','D','A','E','B','#F','#C']
const minorMode = ['♭a','♭e','♭b','f','c','g','d','a','e','b','#f','#c','#g','#d','#a']
//宫调式同自然大调
const shangDorianMode = ['♭D','♭A','♭E','♭B','F','C','G','D','A','E','B','#F','#C','#G','#D']
const juePhrygianMode =   ['♭E','♭B','F', 'C','G','D','A','E','B','#F','#C','#G','#D','#A','#E']
const zhiMixolydianMode =   ['♭G','♭D','♭A','♭E','♭B','F','C','G','D','A','E','B','#F','#C','#G']
const yuAeolianMode =    ['♭A','♭E','♭B','F','C','G','D','A','E','B','#F','#C','#G','#D','#A']
//C伊奥尼亚调式Ionian 同自然大调
const LydianMode = ['♭F','♭C','♭G', '♭D','♭A','♭E','♭B','F','C','G','D','A','E','B','#F']
const LocrianMode = ['♭B','F','C','G','D','A','E','B','#F','#C','#G','#D','#A','#E','#B']

/*
 0  C大调 a小调
 1  G大调 e小调 #F
 2  D大调 b小调 #F #C
 3  A大调#f小调 #F #C #G
 4  E大调#c小调 #F #C #G #D
 5  B大调#g小调 #F #C #G #D #A
 6 #F大调#d小调 #F #C #G #D #A #E
 7 #C大调#a小调 #F #C #G #D #A #E #B
-1  F大调 d小调 bB
-2 bB大调 g小调 bB bE
-3 bE大调 c小调 bB bE bA
-4 bA大调 f小调 bB bE bA bD
-5 bD大调bb小调 bB bE bA bD bG
-6 bG大调be小调 bB bE bA bD bG bC
-7 bC大调ba小调 bB bE bA bD bG bC bF
	*/
const scaleOfFifths = {
	'C': [ 'C', 'D', 'E', 'F', 'G', 'A', 'B', 'C'], //C major
	'G': [ 'G', 'A', 'B', 'C', 'D', 'E','#F', 'G'], //G major
	'D': [ 'D', 'E','#F', 'G', 'A', 'B','#C', 'D'], //D major
	'A': [ 'A', 'B','#C', 'D', 'E','#F','#G', 'A'], //A major
	'E': [ 'E','#F','#G', 'A', 'B','#C','#D', 'E'], //E major
	'B': [ 'B','#C','#D', 'E','#F','#G','#A', 'B'], //B major
	'#F':['#F','#G','#A', 'B','#C','#D','#E','#F'], //#F major
	'#C':['#C','#D','#E','#F','#G','#A','#B','#C'], //#C major

	'F': [ 'F', 'G', 'A', '♭B','C', 'D', 'E', 'F'],
	'♭B':['♭B', 'C', 'D','♭E', 'F', 'G', 'A','♭B'],
	'♭E':['♭E', 'F', 'G','♭A','♭B', 'C', 'D','♭E'],
	'♭A':['♭A','♭B', 'C','♭D','♭E', 'F', 'G','♭A'],
	'♭D':['♭D','♭E', 'F','♭G','♭A','♭B', 'C','♭D'],
	'♭G':['♭G','♭A','♭B','♭C','♭D','♭E', 'F','♭G'],
	'♭C':['♭C','♭D','♭E','♭F','♭G','♭A','♭B','♭C'],
}
const scaleStepName = ['主音','上主音','中音','下属音','属音','下中音','导音']
const chineseScaleStepName = {
	'五声': ['宫','商','角','徵','羽'],
	'清乐': ['宫','商','角','清角','徵','羽','变宫'],
	'雅乐': ['宫','商','角','变徵','徵','羽','变宫'],
	'燕乐': ['宫','商','角','清角','徵','羽','闰'],
}

//const scaleType = ['Major', 'Natrual Minor', 'Harmonic Minor', 'Melodic Minor']
const steps = ['C', 'D', 'E', 'F', 'G', 'A', 'B']
class QScale extends QScore {
	constructor() {
		super()
		this.fifths = 0
	}
	scaleNoteOf(fifths) {
		let key = majorMode[fifths+7]
		let items = scaleOfFifths[key]
		let res = []
		let prevStep = 0
		let octave = 4
		for (let i in items) {
			let item = items[i]
			let stepCh = item.charAt(item.length-1)
			let noteElem = new QNoteElem()
			noteElem.step = steps.indexOf(stepCh)
			if (item.startsWith('♭')) {
				noteElem.alter = -1
			}else if (item.startsWith('#')) {
				noteElem.alter = 1
			}
			if (noteElem.step<prevStep) {
				octave += 1
			}
			prevStep = noteElem.step
			noteElem.octave = octave
			noteElem.setNoteValueLineAcc()
			res.push({note:4, elems:[noteElem]})
		}
		return res
	}
	upScaleOctave(notes) {
		// let key = majorMode[fifths+7]
		// let items = scaleOfFifths[key]
		// let res = []
		let prevStep = 0
		let octave = 4
		for (let i = 0; i < notes.length; i++) {
			let noteElem = notes[i].elems[0]
			if (i===0 && noteElem.step>=5) {
				octave = 3
			}else if (noteElem.step<prevStep) {
				octave += 1
			}
			prevStep = noteElem.step
			noteElem.octave = octave
			noteElem.setNoteValueLineAcc()
		}
	}
	/*config={
		keySignatures: [-7 ... 7]
		scales: ['自然大调', '自然小调','和声小调','旋律小调', 
	'伊奥尼亚调式Ionian', '多利亚调式Dorian','弗里几亚调式Phrygian','利底亚调式Lydian','混合利底亚调式Mixolydian','爱奥尼亚调式Aeolian','洛克里亚调式Locrian']
	}
	*/
	static randomKeyNames(config, num, exceptFifths) {
		let res = []
		let exceptIndex = exceptFifths+7
		for (let i = 0; i < 10; i++) {
			let index = exceptIndex+i+1
			if (index>=majorMode.length) {
				index = exceptIndex-i-1
			}
			if (!config.scales || config.scales.includes('自然大调')) {
				res.push(majorMode[index]+'大调')
			}
			if (!config.scales || config.scales.includes('自然小调')) {
				res.push(minorMode[index]+'小调')
			}
			if (res.length>=num) {
				break
			}
		}
		return res
	}
	copyNote(note) {
		let res = {}
		res.note = note.note
		res.elems = []
		for (let elem of note.elems) {
			res.elems.push(elem.clone())
		}
		return res
	}
	scaleStepName(step) {
		if (this.type === '自然大调' || this.type.endsWith('小调')) {
			if (step>0) {
				return scaleStepName[step]
			}
		}else {
			let type = this.type.substr(0,2)
			let stepNames = chineseScaleStepName[type]
			if (stepNames) {
				let typeIndex = stepNames.indexOf(this.type[2])
				let newStep = (typeIndex+step)%stepNames.length
				if (newStep !== typeIndex) {
					return stepNames[newStep]
				}
			}
		}
	}
	//#g自然小调显示成#g自然大调了。 
	//a自然小调显示成a伊奥尼亚了。
	scaleOf(fifths, scaleType) {
		let notes = this.scaleNoteOf(fifths)
		let name = majorMode[fifths+7]
		let fullname = name+scaleType
		if (scaleType==='自然大调') {
			// name = majorMode[fifths+7]
			// fullname = name+scaleType
		}else if (scaleType.endsWith('小调')) {
			/*
			C自然大调 1、2、3、4、5、6、7、1    全 全 半 全 全 全  半
			a自然小调 6、7、1、2、3、4、5、6 	全 半 全 全 半 全 全
			a和声小调 6、7、1、2、3、4、升5、6	全 半 全 全 半 增二 半
			a旋律小调 上行；6、7、1、2、3、升4、升5、6   下行；6、还5、还4、3、2、1、7、6
			*/
			name = minorMode[fifths+7]
			fullname = name+scaleType
			notes.splice(0, 0, this.copyNote(notes[5]), notes[6])
			notes.splice(8, 2)
			if (scaleType === '和声小调') {
				notes[6].elems[0].alter += 1
			}else if (scaleType === '旋律小调') {
				notes[5].elems[0].alter += 1
				notes[6].elems[0].alter += 1
			}
		}else {
			/*
			C伊奥尼亚调式Ionian Mode 	1、2、3、4、5、6、7、1 自然大调音阶
			D多利亚调式Dorian Mode 	2、3、4、5、6、7、1、2 (2调式)
			E弗里几亚调式Phrygian Mode 3、4、5、6、7、1、2、3 (3调式)
			F利底亚调式Lydian Mode		4、5、6、7、1、2、3、4 (4调式)
			G混合利底亚调式Mixolydian Mode 5、6、7、1、2、3、4、5 (5调式)
			A爱奥尼亚调式Aeolian Mode 6、7、1、2、3、4、5、6 (自然小调)
			B洛克里亚调式Locrian Mode 7、1、2、3、4、5、6、7  （）
			*/
			// name = majorMode[fifths+7]
			// fullname = name+scaleType
			if (scaleType.endsWith('商调式')) {
				name = shangDorianMode[fifths+7]
			}else if (scaleType.endsWith('角调式')) {
				name = juePhrygianMode[fifths+7]
			}else if (scaleType.endsWith('徵调式')) {
				name = zhiMixolydianMode[fifths+7]
			}else if (scaleType.endsWith('羽调式')) {
				name = yuAeolianMode[fifths+7]
			}

			if (scaleType.startsWith('五声')) {
				fullname = scaleType.substr(0,2)+name+scaleType.substr(2)
				/*
				C自然大调 1、2、3、4、5、6、7、1    全 全 半 全 全 全  半
				五声C宫调式 1、2、3、5、6、1 
				b. 商调式  
				五声D商调式 2、3、5、6、1、2 
				*/
				notes.splice(6, 1)
				notes.splice(3, 1)
				if (scaleType.endsWith('商调式')) {
					notes.push(this.copyNote(notes[1]))
					notes.splice(0, 1)
					// notes.splice(1, 4, notes[1], notes[2], notes[3], notes[4])
				}else if (scaleType.endsWith('角调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]))
					notes.splice(0, 2)
					// notes.splice(2, 3, notes[2], notes[3], notes[4])
				}else if (scaleType.endsWith('徵调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]))
					notes.splice(0, 3)
				}else if (scaleType.endsWith('羽调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]))
					notes.splice(0, 4)
				}
			}else if (scaleType.startsWith('清乐') || scaleType.startsWith('雅乐') || scaleType.startsWith('燕乐')) {
				fullname = scaleType.substr(0,2)+name+scaleType.substr(2)
				if (scaleType.startsWith('雅乐')) {
					if (notes[3].elems[0].alter) {
						notes[3].elems[0].alter += 1
					}else{
						notes[3].elems[0].alter = 1
					}
					notes[3].elems[0].setNoteValueLineAcc()
				}else if (scaleType.startsWith('燕乐')) {
					if (notes[6].elems[0].alter) {
						notes[6].elems[0].alter -= 1
					}else{
						notes[6].elems[0].alter = -1
					}
					notes[6].elems[0].setNoteValueLineAcc()
				}
				if (scaleType.endsWith('商调式')) {
					notes.push(this.copyNote(notes[1]))
					notes.splice(0, 1)
				}else if (scaleType.endsWith('角调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]))
					notes.splice(0, 2)
				}else if (scaleType.endsWith('徵调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]))
					notes.splice(0, 4)
				}else if (scaleType.endsWith('羽调式')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]), this.copyNote(notes[5]))
					notes.splice(0, 5)
				}
			}else {
				if (scaleType.startsWith('多利亚')) {
					notes.push(this.copyNote(notes[1]))
					notes.splice(0, 1)
					name = shangDorianMode[fifths+7]
				}else if (scaleType.startsWith('弗里几亚')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]))
					notes.splice(0, 2)
					name = juePhrygianMode[fifths+7]
				}else if (scaleType.startsWith('利底亚')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]))
					notes.splice(0, 3)
					name = LydianMode[fifths+7]
				}else if (scaleType.startsWith('混合利底亚')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]))
					notes.splice(0, 4)
					name = zhiMixolydianMode[fifths+7]
				}else if (scaleType.startsWith('爱奥尼亚')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]), this.copyNote(notes[5]))
					notes.splice(0, 5)
					name = yuAeolianMode[fifths+7]
				}else if (scaleType.startsWith('洛克里亚')) {
					notes.push(this.copyNote(notes[1]), this.copyNote(notes[2]), this.copyNote(notes[3]), this.copyNote(notes[4]), this.copyNote(notes[5]), this.copyNote(notes[6]))
					notes.splice(0, 6)
					name = LocrianMode[fifths+7]
				}
				fullname = name + scaleType 
			}
		}
		this.upScaleOctave(notes)
		this.name = name
		this.fullname = fullname
		this.notes = notes
		if (scaleType === '旋律小调') {
			let downNotes = [
				this.copyNote(notes[6]), 
				this.copyNote(notes[5]), 
				this.copyNote(notes[4]), 
				this.copyNote(notes[3]), 
				this.copyNote(notes[2]), 
				this.copyNote(notes[1]), 
				this.copyNote(notes[0]), 
				]
			downNotes[0].elems[0].alter -= 1
			downNotes[0].elems[0].setNoteValueLineAcc()
			downNotes[1].elems[0].alter -= 1
			downNotes[1].elems[0].setNoteValueLineAcc()
			this.downNotes = downNotes
		}
	}
	/*
	params:
		keySignatures = [-7...7]
		scales: ['自然大调', '自然小调','和声小调','旋律小调', 
		'伊奥尼亚调式Ionian', '多利亚调式Dorian','弗里几亚调式Phrygian','利底亚调式Lydian','混合利底亚调式Mixolydian','爱奥尼亚调式Aeolian','洛克里亚调式Locrian']
		['民族五声宫调式','民族五声商调式','民族五声角调式','民族五声徵调式','民族五声羽调式']
		['清乐宫调式','清乐商调式','清乐角调式','清乐徵调式','清乐羽调式']
	return:
		QScale {
		name:
		notes:
		type: '自然大调', '自然小调'...
		}
	*/
	static randomScale(keySignatures, scales=['自然大调']) {
		let res = new QScale()
		//fifths
		let fifths = null
		if (keySignatures) {
			fifths = keySignatures[QNoteElem.random(keySignatures.length-1)]
		}else {
			fifths = QNoteElem.random(7, -7)
		}
		res.fifths = fifths

		//scale
		let scaleType = scales[QNoteElem.random(scales.length-1)]
		res.scaleOf(fifths, scaleType)
		res.type = scaleType
		return res
	}
}

export default QScale;




