import QNoteElem from './QNote';
import QScore, {chineseNumbers} from './QScore';
import QInterval from './QInterval';

const chordTypes = [3,7,9,11]
const chordNames = {
	3: ['减三和弦', '小三和弦', '大三和弦', '增三和弦'],
	7: ['减七和弦', '半减七和弦', '小七和弦', '大小七和弦', '小大七和弦', '大七和弦', '增大七和弦'],
}
const jazzChordNames = {
	3: ['减三和弦', '小三和弦', '大三和弦', '增三和弦'],
	7: ['减七和弦', '半减七和弦', '小七和弦', '大小七和弦', '属七和弦', '增属七和弦', '大七和弦', '增大七和弦'],
	9: ['减九和弦', '小九和弦', '属九和弦', '大九和弦','增九和弦','小大九和弦','增大九和弦'],
	11: ['小十一和弦', '属十一和弦', '大十一和弦'],
	13: ['小十三和弦', '属十三和弦', '大十三和弦'],
}

const chordInfos={

	//三和弦
	'减三和弦': {types:['小三度','减五度'], step:3, sufix:'dim'}, //1,b3,b5  Cdim
	'小三和弦': {types:['小三度','纯五度'], step:3, sufix:'m'}, //1,b3,5  Cm
	'大三和弦': {types:['大三度','纯五度'], step:3, sufix:''}, //1,3,5  C
	'增三和弦': {types:['大三度','增五度'], step:3, sufix:'aug'}, //1,3,#5  C+ Caug

	//
	'减六和弦': {types:['减三和弦'], inversion: 1, step:6, sufix:'dim6'},  //减三和弦 第1转位
	'小六和弦': {types:['小三和弦'], inversion: 1, step:6, sufix:'m6'},  //小三和弦 第1转位
	'大六和弦': {types:['大三和弦'], inversion: 1, step:6, sufix:'6'},  //大三和弦 第1转位						
	'减四六和弦': {types:['减三和弦'], inversion: 2, step:6, sufix:'dim46'},  //减三和弦 第2转位
	'小四六和弦': {types:['小三和弦'], inversion: 2, step:6, sufix:'m46'},  //小三和弦 第2转位
	'大四六和弦': {types:['大三和弦'], inversion: 2, step:6, sufix:'46'},  //大三和弦 第2转位

	'减七和弦': {types:['减三和弦','减七度'], step:7, sufix:'dim7'}, //1,b3,b5,bb7  Cdim7、C7
	'半减七和弦': {types:['减三和弦','小七度'], step:7, sufix:'ø7'}, //1,b3,b5,b7 	Cø7 
	'小七和弦': {types:['小三和弦','小七度'], step:7, sufix:'m7'}, //1,b3,5,b7  Cmin7、Cmi7、Cm7、C-7
	'大小七和弦': {types:['大三和弦','小七度'], step:7, sufix:'7'}, //1,3,5,b7  Cdominant7、Cdom7、C7	
	'属七和弦': {types:['大三和弦','小七度'], step:7, sufix:'7'}, //1,3,5,b7  Cdominant7、Cdom7、C7
	'增属七和弦': {types:['增三和弦','小七度'], step:7, sufix:'aug7'}, //1,3,#5,b7 Caug7
	'小大七和弦': {types:['小三和弦','大七度'], step:7, sufix:'mmaj7'}, //1,b3,5,7  C-Δ7、Cm(maj7)、C-(maj7)	
	'大七和弦': {types:['大三和弦','大七度'], step:7, sufix:'maj7'}, //1,3,5,7  CΔ7、Cmaj7、Cma7、CM7
	'增大七和弦': {types:['增三和弦','大七度'], step:7, sufix:'maj7(#5)'}, //1,3,#5,7  Cmaj7(#5) CMaj7+5、CM7+ Caug(maj7)、C+(maj7)、C+Δ7

	'七减九和弦': {types:['属七和弦','小九度'], step:7, sufix:'7b9'}, //1,3,(5),b7,b9  C7b9

	//'减九和弦': {types:['减七和弦','大九度'], sufix:'dim9'}, 	//Co9 Cdim9	1,b3,b5,bb7,9
	'小九和弦': {types:['小七和弦','大九度'], step:9, sufix:'m9'}, 		//Cm9  		1,b3,5,b7,9
	'属九和弦': {types:['属七和弦','大九度'], step:9, sufix:'9'}, 		//C9    	1,3,5,b7,9
	'大九和弦': {types:['大七和弦','大九度'], step:9, sufix:'maj9'}, 	//Cmaj9 	1,3,5,7,9
	'增属九和弦': {types:['增属七和弦','大九度'], step:9, sufix:'+9'}, 		//C+9 Caug9 1,3,#5,b7,9
	'小大九和弦': {types:['小大七和弦','大九度'], step:9, sufix:'m(maj9)'}, //Cm(maj9) 	1,b3,5,7,9
	'增大九和弦': {types:['增大七和弦','大九度'], step:9, sufix:'maj9(#5)'}, 	//C+maj9 1,3,#5,7,9

	'小十一和弦': {types:['小九和弦','纯十一度'], step:11, sufix:'m11'}, //1,b3,5,b7,9,11    Cm11
	'属十一和弦': {types:['属九和弦','纯十一度'], step:11, sufix:'11'}, //1,3,5,b7,9,11    C11
	'大十一和弦': {types:['大九和弦','纯十一度'], step:11, sufix:'maj11'}, //1,3,5,7,9,11    Cmaj11

	'小十三和弦': {types:['小十一和弦','大十三度'], step:13, sufix:'m13'}, //1,b3,5,b7,9,11,13    Cm13
	'属十三和弦': {types:['属十一和弦','大十三度'], step:13, sufix:'13'}, //1,3,5,b7,9,11,13    C13
	'大十三和弦': {types:['大十一和弦','大十三度'], step:13, sufix:'maj13'}, //1,3,5,7,9,11,13    Cmaj13
}
/*
1. 属七和弦（大小七和弦）的解决（大调/和声小调的V级和弦）
原位: 5 7 2 4 -> 1 1 1 3
转位: 向I级和弦（1 3 5）就近解决
7 2 4 5 -> 1 1 3 5
2 4 5 7 -> 1 3 5 1
4 5 7 2 -> 3 5 1 1

2. 导七和弦（减七和弦）的解决（和声小调VII级和弦）
向I级和弦（6 1 3）就近解决
原位: #5 7 2 4 -> 6 1 3 3
转位: 
7 2 4 #5 -> 1 3 3 6
2 4 #5 7 -> 3 3 6 1
4 #5 7 2 -> 3 6 1 3
*/

class QChord extends QScore {
	constructor() {
		super()
		this.english = ''
		let note = {note:8, elems:[]}
		this.notes.push(note)
		// this.
		this.type=3 //3,7,9,11
	}
	/*
	config: null为随机和弦
	{
		types: ['大三和弦', '小三和弦', '减三和弦', '增三和弦', '大七和弦', '小七和弦', '大小七和弦', '减七和弦', '半减七和弦', '增大七和弦', '小大七和弦']
		inversions: null 随机转位，或第[0，1，2，3]转位
	}
	*/
	static randomChordNames(config, count, except) {
		var res = []
		let exceptChordInfo = chordInfos[except]
		let exceptStep
		if (exceptChordInfo) {
			exceptStep = exceptChordInfo.step
		}else{
			console.log('Error: randomChordNames except chord is empty')
			console.log(except)
			exceptStep = 7
		}
		for (var i = 0; i < 10; i++) {
			// var type = config && config.types ? config.types[QNote.random(config.types.length-1)] : null
			// var step = chordInfos[type].step
			var step = exceptStep
			if (res.length>1) {
				step=3
			}else if (res.length>2){
				step=7
			}
			var chordType = QChord.getChordNameFromStep(step)
			var chordName = chordType.name
			if (chordName !== except && res.indexOf(chordName)<0) {
				res.push(chordName)
				if (res.length>=count) {
					break
				}
			}
		}
	
		return res
	}
	/*
	params:
		chord: {name: string, english: string, notes:[{step, octave, alter}, ...]}
		level: 第1, 2, 3转位
	return:
		{name: string, english: string, notes:[{step, octave, alter}, ...]}
	*/
	transposeChord(level) {
		if (level !== 1 && level !== 2 && level !== 3) {
			return this
		}
		// console.log(this)
		var res = this //NoteUtil.deepCopy(chord)
		let noteElems = res.notes[0].elems
		var isTriad = (noteElems.length===3)
		var chordType = isTriad ? '三' : '七'
		var newType
		if (noteElems.length===3) {
			if (level===1) {
				newType = '六'
			}else{
				newType = '四六'
			}
		}else{
			if (level===1) {
				newType = '五六'
			}else if(level===2){
				newType = '三四'
			}else{
				newType = '二'
			}
		}
		res.description = '('+res.name+' - 第'+chineseNumbers[level-1]+'转位)'
		res.name = res.name.replace(chordType, newType)
		res.inversion = level

		for (var i = 0; i < level; i++) {
			var note = noteElems[0]
			note.octave += 1
			noteElems.splice(0,1)
			noteElems.push(note)

			note.setNoteValueLineAcc('treble')
		}

		var rootNote = noteElems[0]
		res.english += '/'+rootNote.textOfAlter()+rootNote.textOfStep()
		// console.log(res)
		return res
	}
	/*
	params:
		chordStep: =null:随机， 或3、7、9、11、13和弦
	return:
		{
			step: 3,7,9,11,13
			name: 和弦名字
		}
	*/
	static getChordNameFromStep(chordStep) {
		var names = chordNames[chordStep]
		if (!names) {
			if (chordStep) {
				names = jazzChordNames[chordStep]
			}else{
				chordStep = chordTypes[QNoteElem.random(chordTypes.length-1)]
				names = chordNames[chordStep]
			}
		}
		var chordName = names[QNoteElem.random(names.length-1)]
		return {step: chordStep, name: chordName}
	}

	/*
	return:
		{name: string, english: string, notes:[{note:8, elems:{step, octave, alter}}, ...]}
	*/
	static chordFromRoot(rootNote, chordName) {
		var chordInfo = chordInfos[chordName]
		if (chordInfo) {
			var english = rootNote.textOfAlter()+rootNote.textOfStep()+chordInfo.sufix
			var res = new QChord()
			res.name = chordName
			res.english = english
			for (var i = 0; i < chordInfo.types.length; i++) {
				var type = chordInfo.types[i]
				if (chordInfos[type]) {
					var subChord = QChord.chordFromRoot(rootNote, type)
					if (subChord) {
						res.notes=subChord.notes
					}else{
						return
					}
				}else {
					var interval = QInterval.intervalFromRoot(rootNote, type)
					let noteElems = res.notes[0].elems
					if (interval) {
						if (noteElems.length===0) {
							noteElems.push(interval.root)
						}
						noteElems.push(interval.head)
					}else{
						return
					}
				}
			}
			if (chordInfo.inversion) {
				res = res.transposeChord(chordInfo.inversion)
			}
			return res
		}
	}
	/*
	params:
		config: null为随机和弦
			{
				types: ['大三和弦', '小三和弦', '减三和弦', '增三和弦', '大七和弦', '小七和弦', '大小七和弦', '减七和弦', '半减七和弦', '增大七和弦', '小大七和弦']
				//types: null 随机和弦, 或[3、7、9、11、13] 和弦, 
				inversions: null 随机转位，或第[0，1，2，3]转位
				ignoreUncommonNote: 忽略根音是'#B', '♭C','#E','♭F'的音
			}
	return:
		{
			name: 和弦名字
			description: 描述
			notes: [{note:8, elems:{step, octave, alter, line, value}},...]
		}
	*/
	static randomRootNode(config) {
		var alters = config && config.alters ? config.alters : [-1, 0, 1]
		var alter =  QNoteElem.randomItem(alters) //alters[QNoteElem.random(alters.length-1)]
		let noteElem
		if (config.rootNoteRange && config.rootNoteRange.length>1) {
			noteElem = QNoteElem.randomNoteWithConfig({alter: alter, min: config.rootNoteRange[0], max: config.rootNoteRange[1]})
		}else{
			let line = QNoteElem.random(4, -7)
			noteElem = QNoteElem.noteFromLine(line, alter, 'treble') 

			// noteElem = QNoteElem.randomNote(alter)
		}
		var count = 0
		while(count<10) {
			if ((noteElem.step===6 && noteElem.alter===1)||
				(noteElem.step===0 && noteElem.alter===-1)||
				(noteElem.step===2 && noteElem.alter===1)||
				(noteElem.step===3 && noteElem.alter===-1)
				) {
				noteElem = QNoteElem.randomNote(alter)
			}else {
				break
			}
			count += 1
		}
		return noteElem
	}
	static randomChord(config) {
		var alters = config && config.alters ? config.alters : [-1, 0, 1]
		let noteElem = this.randomRootNode(config)

		var type = config && config.types ? config.types[QNoteElem.random(config.types.length-1)] : null
		var chordType
		if (type) {
			chordType = {name: type, step: chordInfos[type].step}
		}else{
			chordType = QChord.getChordNameFromStep(chordTypes[QNoteElem.random(chordTypes.length-1)])
		}

		// console.log(type)
		let count=0
		while(count<7*10) {
			// console.log(count)
			// console.log(note)
			var chord = QChord.chordFromRoot(noteElem, chordType.name)
			if (chord) {
				chord.type = type
				//设置和弦各音的顺序
				for (var i = 0; i < chord.notes[0].elems.length; i++) {
					chord.notes[0].elems[i].index = i
				}
				//转位
				var transposeLevel = 0
				if (chordType.step<9) {
					transposeLevel = config && config.inversions ? config.inversions[QNoteElem.random(config.inversions.length-1)] : QNoteElem.random(chordType.step===3 ? 2 : 3)
				}
				if (transposeLevel>0) {
					chord = chord.transposeChord(transposeLevel)
				}
				//判断是否符合难度要求
				var wrong = false
				if (alters) {
					let noteElems = chord.notes[0].elems
					for (let i = 0; i < noteElems.length; i++) {
						let n = noteElems[i]
						if (alters.indexOf(n.alter)<0) {
							wrong = true
							// console.log(chord)
							break
						}
					}
				}
				if (!wrong) {
					return chord
				}
			}
			if (transposeLevel>0) {
				noteElem.octave = 4
			}
			if(noteElem.alter<alters[alters.length-1]) {
				noteElem.alter += 1
			}else if (noteElem.step<6) {
				noteElem.step += 1
				//noteElem.alter = -1
				noteElem.alter = alters[0]
			}else{
				noteElem.step = 0
				// noteElem.alter = -1
				noteElem.alter = alters[0]
				// if (note.octave<4) {
				// 	note.octave += 1
				// }
			}
			noteElem.setNoteValueLineAcc()
			count += 1

			if (count>20) {
				alters = null
				noteElem = this.randomRootNode(config)
				// noteElem = QNoteElem.randomNote()
				//chordType = QChord.getChordName(type)
			}
		}
	}
}
export default QChord;



