
export class Beam {
	static Begin = 'begin';
	static End = 'end';
	static Continue = 'continue';
	static Backward = 'backward hook';
	static Forward = 'forward hook';
}

export class Accidental {
	static sharp = 'sharp';
	static flat = 'flat';
	static natural = 'natural';
	static doubleSharp = 'sharp-sharp';
	static doubleFlat = 'flat-flat';
	
	static alterOfAccidental(accidental) {
		if (accidental === this.sharp) {
	      	return 1
	    }else if (accidental === this.flat) {
	        return -1
	    }else if (accidental === this.doubleSharp){
	    	return 2
	    }else if (accidental === this.doubleFlat) {
	    	return -2
	    }else{
	    	return 0
		}
	}
	static accidentalOfAlter(alter) {
		if (alter === 1) {
			return this.sharp
		}else if (alter===2) {
			return this.doubleSharp
		}else if (alter===-1) {
			return this.flat
		}else if (alter===-2) {
			return this.doubleFlat
		}else if (alter===0) {
			return this.natural
		}
	}
}



