import QNoteElem from './QNote';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {chineseNumbers} from './QScore';

import QNoteQuestions from './QNoteQuestions';
import QIntervalQuestions from './QIntervalQuestions';
import QChordQuestions from './QChordQuestions';
import QScaleQuestions, {QKeyQuestions} from './QScaleQuestions';
import QRhythmQuestions from './QRhythmQuestions';
import QMusicTermQuestions from './QMusicTermQuestions';


/*
英皇演奏考试实际上是“三首曲子各占30分，听觉测试占18分，音阶琶音占21分，视奏占21分，共150分”。英皇考级满分150分，100分及格

英皇乐理考级：乐理满分100及格66。

第一级：
1. 全音符、二分音符、四分音符、十六分音符、及对应的休止符、附点音符、附点休止符、连线
2. 2/4,3/4,4/4等单拍子记号，小节线，和拍号。以及以上列出的这些拍子范围内的音符组合。为一个从第一拍开始的指定节奏，再创作两小节的节奏。
3. 谱表、高G低F音谱号、谱表上音符名称，升号、降号、还原记号和还原方法。
4. 大调音阶构成，包括全音和半音的位置。CGDF大调音阶调号，主三和弦，音程度数（只需标明数字），建立在主音上的音程（只需标明数字）。
5、有关速度、力度、演奏指示、发音等常用术语和记号，以及关于记录在高音或低音谱表上的一条旋律的简单问题。

第二级：
2. 增加2/2，3/2，4/2，3/8单拍子记号，三连音。问题包括：根据已有的开头，创作一条从第一拍开始的简单的四小节节奏。
3. 谱表延伸至上下加2线。
4. A大调、降B大调和降E大调以及A小调、E小调和D小调的音阶与调号，及其主三和弦 (原位)、音程度数（只需标明数字），以及建立在主音上的音程（只需标明数字）。
5. 更多音乐术语记号。

第三极：
2. 增加6/8，9/8，12/8复拍子记号，三十二分音符和对应休止符。问题包括：创作一条简单的四小节节奏，可以从弱拍开始。
3. 五线谱两条以上附加线的延伸部分。将一条简单的旋律从高音谱号移至低音谱号，或反之，使用移动八度记号移动一个八度。
4. 四个升降号范围内的所有大小调的音阶与调号，包括小调音阶的和声小调和旋律小调，及其主三和弦（原位）、音程度数（只需标明数字），以及建立在主音上的音程（需标明数字和类型）。
5. 更多的术语和记号。在有关一条旋律的一些简单问题中会涉及到乐句结构的问题。

第四级：
1. 所有单二拍、单三拍、单四拍和复二拍,复三拍,复四拍的拍号，以及在这些节拍范围内的音符和休止符的组合。
二全音符及其相应的休止符。双附点音符及其相应的休止符。二连音。
问题包括：创作一条的四小节节奏，或（由考生选择）根据已有的字词创作一条节奏。
2. 续写旋律，为写出的歌词配节奏。
3. 中音谱号（第三线上的C谱号）。识别这一级别调性范围内的中音谱号上的音符，将记录在高音谱号或低音谱号上的一条简单的旋律按相同音高移至中音谱号上，或反之。
重升和重降记号及其还原方式。等音。
4. 五个升降号范围内的所有大小调的音阶与调号，包括小调音阶的和声与旋律两种形式。
自然音阶中的音符名称 (主音，上主音等)。半音阶的构成。
在这一级别调性范围内的一个八度以内的任意两个自然音之间的所有音程。
5. 识别和书写在这一级别调性范围内的建立在主音、下属音和属音上的三和弦（原位）。
辨认在这一级别调性范围内的建立在主音、下属音和属音上的5/3（原位）和弦（小调中将运用音阶的和声形式）。
6. 更多的术语和记号，包括辨认颤音、回音、上波音和下波音、短倚音和长倚音并说出它们的名称（不用写出）。
关于一段音乐的问题，将包括与标准管弦乐器有关的一些简单问题。

第五级
1、5/4、7/4、5/8、7/8不规则拍号，以及在这些节拍范围内的音符和休止符的组合。单拍子时值的不规则划分。
2. 次中音谱号（第四线上的C谱号）。识别这一级别调性范围内的四种谱号上的音符，并把一条简单的旋律在不同的谱号之间作八度转换。为降B，A或F调乐器谱写一条标准音高的旋律，或反之（会给出向上或向下移调的音程）。采用两个谱表，运用高音和低音谱号用开列总谱写一乐段，或反之。
3. 六个升降号范围内的所有大小调的音阶与调号。从任一音符构成所有的单、复音程。
4．识别在这一级别调性范围内的主和弦、下属和弦和属和弦的5/3、6/3和6/4形式。
识别各种和弦及他们转位的形式，I级四六到V7到I的终止式
识别在这一级别调性范围内的建立在属音上的6/4和5/3(Ic-V)的级数。
使用任何一种公认的标记方法，为C大调，G大调，D大调或F大调的一段简单旋律的终止处选择适当和弦。
5．根据指定的开头，为一件特定的乐器（会提供几个选择）创作一段不超过八小节的简单旋律，或者（由考生选择）根据一段指定的字词创作一条旋律。
需加入适当的有关速度，力度和发音方法的演奏指示。
6．更多的术语和记号。能辨认装饰音，包括根据所描述的实际效果写出适当的装饰音记号，但不用根据装饰音写实际效果。
有关为人声或乐器创作的适于这一级别程度的一段音乐的问题将包括：人声声部的类型和乐器的名称、所运用的谱号，不同的乐器种类及其基本发声原理，以及一些基本的音乐知识要点，以测试考生将理论知识应用于实际音乐的能力。

乐理六级	
1	包含五级所有内容
2	大调与和声大小调中所有三和弦的使用方法，七和弦转位和第一转位，装饰音，转调
3	根据题目给出的数字低音写作四部和声
4	给一段8小节的旋律为和弦做标记或为旋律配上低音声部
5	根据给出开头，续写一段旋律，要求转调到属调，下属调，关系大小调
6	装饰音，和声，跃居，乐曲风格，器乐作品和作曲家

乐理七级	
1	包含六级所有内容
2	所有自然音阶的七和弦及他们的转位，拿波里六和弦和减七和弦，数字低音
3	用数字低音标示和弦及内声部的进行
4	改写乐段，添加延留音，经过音和装饰音
5	根据开头，为含有键盘伴奏的独奏乐器声部续写一段旋律
6	考察学生对装饰音，基本和声，乐句结构，乐曲风格及演奏方式的识别，了解声乐，器乐作品和作曲家

乐理八级	
1	包含七级所有内容
2	完成巴洛克三重奏名曲选段，根据通奏低音下面给出的数字低音，完成上二声部和低音声部。
3	根据选段概要完成一个键盘乐器，选段设计到古典之后的音乐风格
4	根据已给出的开头，为指定乐器续写一段旋律
5	考察学生对装饰音，基本和声，乐句结构，乐曲风格及演奏方式的识别，了解声乐，器乐作品和作曲家

分类：
1. 拍子与节奏 Time and Rhythm
2. 音高Pitch
3. 音阶Scales
4. 音程Intervals
5. 和弦 Chords
6. 演奏指示 Performance Directions

*/

class QExamQuestions {
	/*
	config={
		level: 1...8
	}
	*/
	static genQuestions(config) {
		let questions = []
		
		let noteQuestions = QNoteQuestions.genQuestions(config)

		return questions
	}
	/*
	1. 全音符、二分音符、四分音符、十六分音符、及对应的休止符、附点音符、附点休止符、连线
	2. 2/4,3/4,4/4等单拍子记号，小节线，和拍号。以及以上列出的这些拍子范围内的音符组合。为一个从第一拍开始的指定节奏，再创作两小节的节奏。
	3. 谱表、高G低F音谱号、谱表上音符名称，升号、降号、还原记号和还原方法。
	4. 大调音阶构成，包括全音和半音的位置。CGDF大调音阶调号，主三和弦，音程度数（只需标明数字），建立在主音上的音程（只需标明数字）。
	5、 有关速度、力度、演奏指示、发音等常用术语和记号，以及关于记录在高音或低音谱表上的一条旋律的简单问题。
	*/

}

export default QExamQuestions;



