import React from 'react';
import Glyph from './MusicGlyph';
import '../App.css';

export const iconfont = {
	noteWhole: '\ue6ae',
	noteHalf: '\ue6a5',
	noteQuarter: '\ue6b6',
	note8th: '\ue6a2',
	note16th: '\ue6b8',
	note32nd: '\ue6bd',

	restWhole: '\ue6b5',
	restHalf: '\ue6af',
	restQuarter: '\ue6ab',
	rest8th: '\ue6a3',
	rest16th: '\ue6c0',
	rest32nd: '\ue6ac',

	accNatural: '\ue6b1',
	accSharp: '\ue6ba',
	accFlat: '\ue6a8',

	arrowDown: '\ue6bf',
	arrowUp: '\ue6aa',
	arrowLeft: '\ue6b0',
	arrowRight: '\ue6b9',
	arrowChordSwitch: '\ue6a9',

	clefTreble: '\ue6a6',
	clefBass: '\ue6a0',

	dotted: '\ue6a7',
	tuplet: '\ue6a1',
	barline: '\ue6ad',
	tied: '\ue6b7',
	zoomin: '\ue6b2',
	beam: '\ue6b4',

	minus: '\ue601',
	plus: '\ue605',
	redo: '\ue6bc',
	undo: '\ue6be',
	delete: '\ue6bb',
	load: '\ue6b3',
	save: '\ue6a4',
}
export const iconcommon = {
	arrowUp: '\ue63b',
	arrowDown: '\ue650',
	arrowLeft: '\ue660',
	arrowRight: '\ue65b',
	selectOne: '\ue706',
	selectRow: '\ue708',
	zoomout: '\ue6ba',
	zoomin: '\ue6bc',
	success: '\ue6de',
}

export const color = {
	pink: 'linear-gradient(0deg, #b12484, #d177b4)',
	red: 'linear-gradient(0deg, #e00152, #f65c6a)',
	orange: 'linear-gradient(0deg, #8a4613, #fd8500)',
	green: 'linear-gradient(0deg, #7eb520, #9fd825)',
	darkGreen: 'linear-gradient(0deg, #447022, #84b062)',
	puple: 'linear-gradient(0deg, #743db7, #b57eff)',
	blue: 'linear-gradient(0deg, #1452a1, #369bdf)',
	control: 'linear-gradient(0deg, #e15b01, #ffba8c)',

	black: 'linear-gradient(0deg, #777, #999)',
	white: 'linear-gradient(0deg, #eee, #fff)',
}

function onKey(key, down) {
	console.log('onKey:'+key+' '+down)
	if (window.buttons && down) {
		for (var i=0; i<window.buttons.length; i++) {
			const item = window.buttons[i];
			if (item.key === key) {
				window.toolbar.onClick(item)
				return 'true'
			}
		}
	}
	if (key===108) {
		if (window.theApp.state.windowHistory.length===1) {
			return  'false'
		}else if (down){
			window.theApp.popWindow()
		}
	}else if (window.staff) {
		if (down) {
			if (!window.pressedKeys) {
				window.pressedKeys = []
			}
			window.pressedKeys.push(key)
		}else{
			if (window.pressedKeys && window.pressedKeys.length>0) {
				window.staff.inputNoteKeys(window.pressedKeys)
				window.pressedKeys = []
			}
		}
	}
	return 'true'
}

class Toolbar extends React.Component {
	onClick(item) {
		if (item && item.onClick) {
			item.onClick(item)
		}else if (item && item.action && this.props.action) {
			this.props.action(item.action)
		}
	}
	systemButtons(btn={arrow: true, ok: 'OK'}) {
		let buttons = []
	    let keys = [41,42,43,44,45]
	    if (window.device === 'piano') {
	    	keys = [73,75,78,80,82]
	    }
	    if (btn.arrow) {
	    	buttons.push({iconcommon:iconcommon.arrowLeft, key: keys[0], action: {type: 'moveColumn', offset: -1}, background: color.blue})
	    	buttons.push({iconcommon:iconcommon.arrowRight, key: keys[1], action: {type: 'moveColumn', offset: 1}, background: color.blue})
	        buttons.push({iconcommon:iconcommon.arrowUp, key: keys[2], action: {type: 'moveRow', offset: -1}, background: color.blue})
	    	buttons.push({iconcommon:iconcommon.arrowDown, key: keys[3], action: {type: 'moveRow', offset: 1}, background: color.blue})
	    }
	    if (btn.ok) {
	        buttons.push({title:btn.ok, key: keys[4], action: {type: 'OK'}, background: color.control})
	    }
        return buttons
	}
	render() {
		let items = this.props.items;
		if (this.props.systemButtons) {
			if (!items) {
				items = []
			}
			items.push(...this.systemButtons(this.props.systemButtons))
		}
		// if (window.device === 'piano' && window.history.length>0) {
		// 	items.push({title: '首页', key: 97, onClick: (e)=>{window.location.href='/'; }, background: color.control})
		// 	// items.push({title: '返回', key: 99, onClick: (e)=>{window.history.back();}, background: color.control})
		// }

		window.buttons = items;
		window.onKey = onKey;
		window.toolbar = this;
		const buttons = []

		const width =  window.document.body.offsetWidth


		//piano simulator
		var keyStart = 20
		var keyWidth = width/89
		var skipMidKey = 0
		
		//piano
		// var keyStart = 26
		// var keyWidth = width/77.5
		// var skipMidKey = 10

		//phone
		if (window.device === 'mobile') {
			keyStart = 35
			keyWidth = width/24
			skipMidKey = 0
			if (keyWidth<30) {
				keyWidth = 30
			}
		}else if(window.device === 'pc') {
			keyStart = 30
			keyWidth = width/60
			skipMidKey = 0
			if (keyWidth<32) {
				keyWidth = 32
			}
		}
		let cellWidth = keyWidth*2
		if (window.device !== 'piano') {
			cellWidth = keyWidth*1.2
		}
		const glyphFontSize = keyWidth*0.26;
		let rows=Math.floor(items.length*cellWidth/width)+1
		if (window.device === 'piano') {
			rows = 1
		}
		let toolbarHeight = keyWidth*1.6*rows
		let y = 0
		let x = -cellWidth/2
		for (let i = 0; i < items.length; i++) {
			const item = items[i];
			// let x
			if (window.device === 'piano') {
				x=(item.key-keyStart-0.5)*keyWidth;
				if (item.key>64) {
					x+=skipMidKey
				}
			}else{
				x += cellWidth
				if (x+cellWidth>width) {
					x=cellWidth/2
					y+=keyWidth*1.6
				}
			}
			const color = item.color ? item.color : 'white'
			var content = ''
			var fontSize = glyphFontSize;
			if (item.title) {
				content = item.title
				fontSize = keyWidth*0.44;
				if (content.length===1) {
					fontSize *= 1.5
				}
			}else if (item.note) {
				content = <Glyph params={{type: 'note', note:item.note, rest: item.rest}} color={color} fontSize={fontSize} />
			}else if (item.type && item.type === 'accidental') {
				content = <Glyph type='accidental' params={{value:item.value}} color={color} fontSize={fontSize} />
			}else if (item.clef) {
				content = <Glyph type='clef' params={{value:item.clef}} color={color} fontSize={fontSize*0.7} />
			}else if (item.image) {
				content = <img src={item.image} alt='' width={keyWidth*0.8} height='100%' />
			}else if (item.iconfont) {
				content = <div className='iconfont' style={{fontSize:keyWidth*0.8}}>{item.iconfont}</div>
			}else if (item.iconcommon) {
				content = <div className='iconfontcommon' style={{fontSize:keyWidth*0.8}}>{item.iconcommon}</div>
			}
			const background = item.background ? item.background : 'linear-gradient(0deg, black, #aaa)' //'#595'
			const styleButton = {position: 'absolute', left: x, top: y, fontSize: fontSize, background:background, width: keyWidth, height: keyWidth, lineHeight: keyWidth+'px', textAlign:'center', color: color, borderRadius: keyWidth/4}
			
			var belowElem = null
			if (item.subTitle) {
				const textHeight = keyWidth*0.5
				const styleText = {position: 'absolute', left: -keyWidth/2, top: keyWidth+textHeight/5, width: keyWidth*2, lineHeight: textHeight+'px', textAlign:'center', color: 'white', fontSize: keyWidth*0.4}
				belowElem = <div key={100+i} style={styleText}>{item.subTitle}</div>
			}else if (window.device==='piano'){
				const dotWidth = keyWidth*0.3
				const styleDot = {position: 'absolute', left: keyWidth/2-dotWidth/2, top: keyWidth+dotWidth/2, background: background, width: dotWidth, height: dotWidth, borderRadius: dotWidth/2}
				belowElem = <div key={100+i} style={styleDot}/>
			}
			let topElem = null
			if (item.topTitle) {
				const textHeight = keyWidth*0.5
				let top = -textHeight
				if (item.topTitle.length>5) {
					top -= textHeight
				}
				let fsize = keyWidth*0.35
				if (window.device !== 'piano') {
					fsize = keyWidth*0.26
				}
				const styleText = {position: 'absolute', left: -keyWidth/2, top: top, width: keyWidth*2, lineHeight: textHeight+'px', textAlign:'center', color: 'black', fontSize: fsize}
				topElem = <div key={100+i+1} style={styleText}>{item.topTitle}</div>
			}
			buttons.push(
				<div key={i} style={styleButton} onClick={(e) => this.onClick(item)}>
				{content}
				{topElem}
				{belowElem}
				</div>
				)
		}
	return (
		<div style={{position:'fixed', bottom:toolbarHeight}}>
			{buttons}
		</div>
		)
	}
}

export default Toolbar;



