import QNoteQuestions from './QNoteQuestions';
import QIntervalQuestions from './QIntervalQuestions';
import QChordQuestions from './QChordQuestions';
import QScaleQuestions, {QKeyQuestions} from './QScaleQuestions';
import QRhythmQuestions from './QRhythmQuestions';
import QMusicTermQuestions from './QMusicTermQuestions';
import QExamQuestions from './QExamQuestions';

/*
//题型：
1. 基本乐科
1.1 乐理
	单音（一级）：
		a. 下列选项（五线谱）与给定唱名(Do, Re,...)一致的是：
		b. 选择音符（五线谱）的正确唱名(Do, Re,...)
	音程（一级两个选项，二级二三度，三级如大小六度）
		a. 选择音程（给定一个音程的五线谱，选择音程名称）
	和弦（二级旋律3和弦， 三级和声3和弦）
		a. 和弦识别（给定一个旋律3和弦的五线谱，选择和弦名称）
	节奏节拍
		a.（1，2级） 计算下列（2/4拍五线谱中）方框中（音符）的节奏时值。（半拍、一拍， 一拍半）
		b.（3级） 根据拍号，乐谱小节数是：（有拍号，带浮点，无小节线，选项：三小节、四小节）
	旋律调式
		a. （2级）选择正确的调式。（五线谱显示一个调式，选择c自然大调、a自然小调。。。）
		b. （3级）辨别音级，哪一个是a和声小调，如：第VII级，选项：五线谱里的一个全音符。
	音乐符号
		a. （1，2级）识别符号（如五线谱里的pp/mp, 选项：弱，很弱。。。）
		a. （3级）符号识别（ru五线谱里的Lento/Andante/Allegro, 选项：慢板，行板，快板。。。）
	音乐常识（1，2级）
		a. 识别图中的乐器（如长笛、双簧管、大管、短笛、单簧管。。。）
1.2 听辨
	单音听辨
		a. （1级） 听辨音高：标准音之后，第几个音最低。
		b. （2级） 单音听辨：标准音之后，第几个音是Do，Fa。 （标准音后随机播放三个音） 
		c. （3级） 五音组中，哪一个音最高。（播放5个音，选出其中两个音，问哪个音最高）
	音程听辨
		a. （1级）听辨三个音程中第几个距离最近。（先放一个标准音，然后放三个旋律音程）
		b. （2级）根据音响，听辩旋律音程的度数。（先放一个标准音，然后放1个旋律音程，两个选项）
		c. （3级）听辩音程，选择正确的音程性质。（先放一个标准音，然后放1个和声音程，两个选项大二度、大小三度， 纯四度）
	和弦听辩
		a. （2级）(先放一个标准音，然后放1个旋律大小三和弦，两个选项)
		b. （3级）听辩哪一个是大3和弦(先放一个标准音，然后放2个和声三和弦，选择哪个是大3和弦)
		
	节奏节拍听辨（1、2级， 二级增加浮点节奏，三连音）
		a. （1，2级）根据音响，选择（单线谱里）方框中（方框里空空缺）的节奏。（选项是两个节奏谱，没有五线）
		b. （3级）听辩节奏，与音响一致的是。（选项是两个单线谱节奏）
	旋律调式听辨
		a. （1、2级）听辩方框内的音符是。
		（1级：一首五线谱的方框内缺少一个音符，选项是3个音高不同的四分音符）
		（2级：一首五线谱的方框内缺少一小节或一段节奏，选项是3个相同节奏（浮点或3连音节奏），不同音高的五线谱）
		b. (3级) 听辩旋律，乐曲的速度是。（选项：快板、慢板、行板。。。）
1.3 全真模拟
1.4 乐科视唱
	连续播放四段五线谱，要求学生分段唱出
*/


class Questions {
	static genQuestionsTo(questions, Class, config) {
		let q = Class.genQuestions(config)
		if (q) {
			questions.push(...q)
		}
	}
	/*
	config: {
		type: 'interval' or 'chord' or 'scale'
		level: null 随机难度， 1-5级难度
	}
	return:[Question]
	question:{
		desc: '',
		score: {notes: []},
		choices: [string]
		answer: {notes: []} or index of choices
	}
	*/
	static genQuestions(config, num=1) {
		var questions = []
		for (var i = 0; i < num; i++) {
			if (!config.type || config.type==='note') {
				this.genQuestionsTo(questions, QNoteQuestions, config)
			}
			if (!config.type || config.type==='interval') {
				this.genQuestionsTo(questions, QIntervalQuestions, config)
			}
			if (!config.type || config.type==='chord') {
				this.genQuestionsTo(questions, QChordQuestions, config)
			}
			if (!config.type || config.type==='keysignature') {
				this.genQuestionsTo(questions, QKeyQuestions, config)
			}
			if (!config.type || config.type==='scale') {
				this.genQuestionsTo(questions, QScaleQuestions, config)
			}
			if (!config.type || config.type==='rhythm') {
				this.genQuestionsTo(questions, QRhythmQuestions, config)
			}
			if (!config.type || config.type==='terms') {
				this.genQuestionsTo(questions, QMusicTermQuestions, config)
			}
			if (!config.type || config.type==='exam') {
				this.genQuestionsTo(questions, QExamQuestions, config)
			}
		}
		return questions
	}
}

export default Questions;






