import React from 'react';
import {lineh} from '../config/MusicConfig';

class Musfont extends React.Component {
  render() {
    var style = {
      fontSize: this.props.fontSize,
      width: this.props.fontSize*2,
    }

    if (this.props.left) {
      style.position = 'absolute';
      style.left = this.props.left;
    };
    if (this.props.top || this.props.top===0) {
      style.top = this.props.top;
    }
    if (this.props.color) {
      style.color = this.props.color;
    }
    return (<div className='App-musfont' style={style} onClick={this.props.onClick}> {this.props.glyph} </div>)
  }
}
export class Line extends React.Component {
  render() {
    let color = this.props.color ? this.props.color : 'black'
    return (<div style={{position:'absolute', left: this.props.left, top: this.props.top, width: this.props.width, height: this.props.height, background: color}}></div>)
  }
}
export class ExLine extends React.Component {
  render() {
    const x1 = this.props.x1
    const y1 = this.props.y1
    const x2 = this.props.x2
    const y2 = this.props.y2
    const width = Math.sqrt((x2-x1)*(x2-x1)+(y2-y1)*(y2-y1))
    const height = this.props.width
    const deg = Math.atan((y2-y1)/(x2-x1))
    const top = (y2+y1)/2
    const left = (x2+x1-width)/2
    //console.log('x1:'+x1+' x2:'+x2+' y1:'+y1+' y2:'+y2+' w:'+width+' h:'+height+' deg:'+deg)
    const style = {
      position:'absolute', 
      left: left,
      top: top, 
      width: width,
      height: height,
      background: this.props.color ? this.props.color : 'black',
      transform: 'rotate('+deg*180/3.1415926+'deg)'
      // -o-transform:rotate(125deg);
      // -moz-transform:rotate(125deg);  
      // -webkit-transform:rotate(125deg); 
      }
    return (<div style={style}></div>)
  }
}


export class Fiveline extends React.Component {
  render() {
    const lineWidth = lineh>15 ? lineh/15 : 1
    const style = {marginTop: lineh-lineWidth, height: lineWidth, marginLeft: this.props.left}
    return (
      <div style={{position: 'absolute', width: '100%', top: this.props.top+2.92*lineh}}>
      <div className='App-line' style={style}></div> 
      <div className='App-line' style={style}></div>
      <div className='App-line' style={style}></div>
      <div className='App-line' style={style}></div>
      <div className='App-line' style={style}></div>
      </div>
      )
  }
}
export class Bracket extends React.Component {
  render() {
    const height = this.props.height
    let scale = 'scale('+height/160+','+height/170+')'
    return (
      <div style={{position: 'absolute', left:this.props.x, top:this.props.y}}>
        <svg xmlns='http://www.w3.org/2000/svg' version='1.1' style={{height: height}}>
          <path 
              stroke='black' 
              strokeWidth='0.5' 
              d='M5.25,147.193c-0.24-7.15,0.685-14.153,1.633-21.169c0.898-6.642,1.897-13.29,2.368-19.987c0.588-8.364-1.47-15.642-7.212-20.834C1.968,85.139,1.941,85,1.877,84.859c0.28-0.276,0.533-0.528,0.787-0.777c3.551-3.498,5.676-7.998,6.368-13.546C9.719,65.025,9.131,59.597,8.43,54.17c-1.021-7.896-2.264-15.76-2.995-23.696C4.629,21.723,5.482,13.13,8.772,5.138C9.422,3.559,9.603,2.035,9.338,0C9.16,0.346,9.104,0.454,9.049,0.563C1.763,14.979-1.077,30.265,3.006,46.936c1.2,4.899,2.391,9.807,3.445,14.756c1.833,8.603,0.066,15.988-5.222,22.071C0.913,84.125,0.558,84.439,0,84.997c0.477,0.419,0.796,0.642,1.058,0.94c5.147,5.86,7.227,12.978,5.515,21.455c-1.073,5.311-2.437,10.533-3.62,15.812c-1.568,6.994-2.294,14.082-1.516,21.346c0.938,8.762,3.664,16.687,7.324,24.19c0.158,0.322,0.338,0.629,0.594,1.104c0.208-1.895,0.118-3.409-0.548-5.001C6.496,159.325,5.458,153.38,5.25,147.193z' 
              fill='black' 
              transform={scale}/>
        </svg>
      </div>
      )
  }
}
export default Musfont;



