import QNoteElem from './QNote';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';

const noteNames=['C','D','E','F','G','A','B']
class QNoteQuestions extends BaseQuestions {
	/*
	从第二级开始考上下加2线
	config={
		level: 1...5
		clefs: treble or bass
		notes: ['C','#C','D','♭E','E','F','#F','G','♭A','A','♭B','B']
		rootNoteRange:  [start, end]
		noteRange: ['识别音域']
		noteTimeValue: ['音符','休止符', '附点']
	}
	*/
	static genQuestions(config) {
		config.clef = this.randomClef(config)
		let questions = []
		if (!config.qtypes || config.qtypes.includes('Identification')) {
			if (config.noteTimeValue && QNoteElem.random(1)) {
				this.randomNoteTerm(questions, config.noteTimeValue)
			}else{
				this.questionIdentification(questions, config)
			}
		}
		if (!config.qtypes || config.qtypes.includes('Construction')) {
			this.questionConstruction(questions, config)
		}
		if (!config.qtypes || config.qtypes.includes('Keyboard')) {
			this.questionKeyboard(questions, config)
		}
		if (!config.qtypes || config.qtypes.includes('EarTraining')) {
			this.questionEarTraining(questions, config)
		}

		return questions
	}
	static noteTypeNames = ['二全', '全', '二分', '四分', '八分', '16分', '32分', '64分'];
	static noteTypes = ['d2', 1, 2, 4, 8, 16, 32, 64];
	static randomNoteTerm(questions, noteTerms) {
		let choiceNames = [...this.noteTypeNames, '休止符', '附点', '双附点']
		let noteIndex = QNoteElem.random(this.noteTypes.length-1)
		let noteType = this.noteTypes[noteIndex]

		let rest = false
		if (noteTerms.includes('音符') && noteTerms.includes('休止符')) {
			if (QNoteElem.random(1)) {
				rest = true
			}
		}else if (noteTerms.includes('休止符')) {
			rest = true
		}

		let choiceIndexs = [noteIndex]
		if (rest) {
			choiceIndexs.push(choiceNames.indexOf('休止符'))
		}
		let dots = 0
		if (noteTerms.includes('附点')) {
			dots = QNoteElem.random(2)
			if (dots===1) {
				choiceIndexs.push(choiceNames.indexOf('附点'))
			}else if (dots===2) {
				choiceIndexs.push(choiceNames.indexOf('双附点'))
			}
		}

		let question = new Question()
		question.desc = '选择正确名称'
		let note = {note: noteType, dots: dots}
		if (rest) {
			note.rest = true
		}else{
			note.elems = [{line: 0}]
		}
		question.score = {clef: 'none', notes: [note]}
		question.choices = choiceNames
		question.answers = choiceIndexs
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
	static questionIdentification(questions, config) {
		let note = QNoteElem.randomNote(0, config.clef)
		let noteName = noteNames[note.step]
		let choices = ['C','D','E','F','G','A','B']
		let answers = [choices.indexOf(noteName)]
		// if (note.alter===1) {
		// 	answers.push(7)
		// }else if (note.alter===-1) {
		// 	answers.push(8)
		// }
		if (config.noteRange && config.noteRange.length>0) {
			answers.push(choices.length+note.octave-2)
			choices.push('大字一组', '大字组', '小字组', '小字一组', '小字二组', '小字三组', '小字四组')
		}

		let score = {clef: config.clef, notes:[{note:1,elems:[note]}]}
		var question = new Question()
		question.desc = '选择音名'
		if (config.noteRange && config.noteRange.length>0) {
			question.desc += '和音域'
		}
		question.score = score
		question.choices = choices
		question.choicesGroup = [7]
		// question.answer = answer
		question.answers = answers
		question.keyboard = KeyboardType.Choice
		
		questions.push(question)
	}
	/*
	保持原来的音高，用高音谱表（低音谱表）重写下列音符或旋律。
	*/
	static questionConstruction(questions, config) {
		let note = QNoteElem.randomNote(null, config.clef)
		let noteName = noteNames[note.step]
		if (note.alter===1) {
			noteName += '#'
		}else if (note.alter === -1) {
			noteName += '♭'
		}
		var question = new Question()
		question.desc = '写出'+noteName+'音'
		question.score = {fifths: 0, clef: config.clef}
		question.answers = {ignoreOctave: true, notes: [{note:1,elems:[note]}]}
		question.keyboard = KeyboardType.WriteNotes
		
		questions.push(question)
	}
	static genNextNote(config) {
		let prevNoteValue = this.prevNoteValue

		let line = QNoteElem.random(6, -6)
		let curAlter = QNoteElem.random(1, -1)
		let note = QNoteElem.noteFromLine(line, curAlter, config.clef) 

		if (prevNoteValue) {
			if (note.value === prevNoteValue) {
				console.log('note is same:'+prevNoteValue+' to '+note.value)
				if (line>0) {
					line -= 1
				}else{
					line += 1
				}
				note = QNoteElem.noteFromLine(line, curAlter, config.clef) 
				console.log('change to:'+note.value)
			}else if (note.value>prevNoteValue+12) {
				console.log('note is too big:'+prevNoteValue+' to '+note.value)
				line -= note.value>prevNoteValue+20 ? 10 : 5
				if (line<-6 && config.clef==='treble') {
					line = QNoteElem.random(6, -1)
					config.clef = 'bass'
				}
				note = QNoteElem.noteFromLine(line, curAlter, config.clef) 
				console.log('change to:'+note.value)
			}else if (note.value<prevNoteValue-12) {
				console.log('note is too small:'+prevNoteValue+' to '+note.value)
				line += note.value<prevNoteValue-20 ? 10 : 5
				if (line>6 && config.clef==='bass') {
					line = QNoteElem.random(1, -6)
					config.clef = 'treble'
				}
				note = QNoteElem.noteFromLine(line, curAlter, config.clef) 
				console.log('change to:'+note.value)
			}
		}

		this.prevNoteValue = note.value
		return note
	}
	static questionKeyboard(questions, config) {
		//let note = QNoteElem.randomNote(null, config.clef)
		let note = this.genNextNote(config)
		// let noteName = noteNames[note.step]
		// if (note.alter===1) {
		// 	noteName += '#'
		// }else if (note.alter === -1) {
		// 	noteName += '♭'
		// }
		var question = new Question()
		question.desc = '弹出五线谱里的音' //+noteName+` clef: ${config.clef}, step:${note.step}, octave:${note.octave}, value:${note.value}`
		question.score = {fifths: 0, clef: config.clef, notes:[{note:1, elems:[note]}]}
		question.keyboard = true
		question.answers = {notes:[{note:1, elems:[note]}]}
		question.keyboard = KeyboardType.PlayKeys

		questions.push(question)
	}

	/*
	config: { 
		notes: ['C','#C','D','♭E','E','F','#F','G','♭A','A','♭B','B']
		rootNoteRange:  [start, end]
	}
	*/
	static questionEarTraining(questions, config) {

		let noteIndex = QNoteElem.random(config.notes.length-1)
		let noteName = config.notes[noteIndex]
		let notes = ['C','#C','D','♭E','E','F','#F','G','♭A','A','♭B','B']
		let noteValue = notes.indexOf(noteName)

		var question = new Question()
		if (config.rootNoteRange && config.rootNoteRange.length>1) {
			let min = QNoteElem.noteValueOfXn(config.rootNoteRange[0])
			let max = QNoteElem.noteValueOfXn(config.rootNoteRange[1])
			let octave1 = (min-noteValue)/12
			let octave2 = (max-noteValue)/12

			let note = new QNoteElem()
			note.octave = QNoteElem.random(octave2, octave1)
			if (note.octave<octave1) {
				note.octave = Math.floor(octave1+0.99)
			}else if (note.octave>octave2) {
				note.octave = Math.floor(octave2)
			}
			note.step = ['C','D','E','F','G','A','B'].indexOf(noteName[noteName.length-1])
			note.alter = noteName.startsWith('#') ? 1 : noteName.startsWith('♭') ? -1 : 0
			note.setNoteValueLineAcc('treble')
		
			//和前一次音不要超出8度
			let prevNoteValue = this.prevNoteValue
			console.log('listen: '+note.value)
			if (prevNoteValue) {
				let changed = false
				if (note.value===prevNoteValue) {
					if (noteIndex<config.notes.length-1) {
						noteIndex+=1
					}else{
						noteIndex-=1
					}
					noteName = config.notes[noteIndex]
					note.step = ['C','D','E','F','G','A','B'].indexOf(noteName[noteName.length-1])
					note.alter = noteName.startsWith('#') ? 1 : noteName.startsWith('♭') ? -1 : 0
					changed = true
				}else if (note.value>prevNoteValue+24 && note.octave>octave1+1) {
					note.octave -= 2
					changed = true
				}else if (note.value>prevNoteValue+12 && note.octave>octave1) {
					note.octave -= 1
					changed = true
				}else if (note.value<prevNoteValue-24 && note.octave<octave2) {
					note.octave += 2
					changed = true
				}else if (note.value<prevNoteValue-12 && note.octave<octave2) {
					note.octave += 1
					changed = true
				}
				if (changed) {
					note.setNoteValueLineAcc('treble')
					console.log('to: '+note.value)
				}
			}
			this.prevNoteValue = note.value

			noteValue = note.value
			if (noteValue>max || noteValue<min) {
				console.log('error: to random note')
				console.log(note)
			}
			//question
			question.desc = '弹出听到的音' //+noteName+` step:${note.step}, octave:${note.octave}, alter:${note.alter}, value:: ${note.value}`
			question.keyboard = KeyboardType.PlayKeys
			question.answers = {notes:[{note:1, elems:[note]}]}
		}else{
			noteValue += 48
			question.desc = '选择听到的音'
			question.choices = config.notes
			question.answers = [noteIndex]
			question.keyboard = KeyboardType.Choice
		}
		question.midi = {events:[{tick: 0, note: noteValue, duration: 480}]}
		// console.log('play:'+noteValue+' answer:'+noteIndex)
		questions.push(question)
	}
}

export default QNoteQuestions;



