/*
踏板作为shift键，踩下工具条改变功能。
加个显示各种表情记号。
中间可以变谱号、调号、拍号。
1234
*/

function inputNotes(keys) {
  window.staff.inputNoteKeys(keys)
}
function openMusicFile(str) {
  window.staff.openMusicFile(str)
}

function onPedalKey(key) {
  //4个踏板：0x7F74, 0x7F75, 0x7F76, 0x7F77
  if (key === 0x7F74) {
    window.staff.moveMeasure(-1)
  }else if (key === 0x7F75) {
    window.staff.moveMeasure(1)
  }else if (key === 0x7F76) {
    // window.staff.moveMeasure(-1)
  }else if (key === 0x7F77) {
    // window.staff.moveMeasure(1)
  }
}
