import QNoteElem from './QNote';
import QInterval from './QInterval';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {chineseNumbers} from './QScore';

class QIntervalQuestions extends BaseQuestions {
	/*
	音程（一级两个选项，二级二三度，三级如大小六度）
		a. 选择音程（给定一个音程的五线谱，选择音程名称）
	1. 八度以内的自然音程（纯白键）
	2. 八度以内的自然音程（带升，降，还原）
	3. 十五度以内的自然音程（带升，降，还原）
	4. 八度以内的变化音程（包含重升，重降记号）倍增和倍减
	5. 十五度以内的变化音程（带重升，重降）倍增和倍减
	config={
		level: 1...5
		types: ['减','小','纯','大','增']
		steps: [1，2，3,... 15]
	}
	*/
	static genQuestions(config) {
		// config.clef = this.randomClef(config)
		let questions = []
		if (config.level) {
			config = {}
			if (config.level<3 || config.level===4) {
				config.steps = [1,2,3,4,5,6,7,8]
			}
			if (config.level===1) {
				config.alters = [0]
			}else if (config.level===2 || config.level===3) {
				config.alters = [-1, 0, 1]
			}

			if (config.level < 4) {
				config.types = ['减', '小','纯','大', '增']
			}
		}
		if (config.difficulty) {
			if (config.difficulty[0].startsWith('1')) {
				config.alters = [0]
			}else if (config.difficulty[0].startsWith('2')) {
				config.alters = [-1, 0, 1]
			}else {
				config.alters = [-2, -1, 0, 1, 2]
			}
		}
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.intervalQuestionIdentification(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			if (QNoteElem.random(1)) {
				this.intervalQuestion1(questions, config)
			}else{
				this.intervalQuestion2(questions, config)
			}
		}
		if (!config.qtypes ||  config.qtypes.includes('Keyboard')) {
			this.intervalQuestionKeyboard(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('EarTraining')) {
			// config.steps=[2,3,4,5,6,7,8]
			// config.types=['减','小','纯','大','增']
			this.intervalQuestionEarTraining(questions, config)
		}
		return questions
	}
	static createRandomChooseQuestion(config) {
		var interval = QInterval.randomInterval(config)
		if (!interval.name) {
			console.log('error: to generate random interval')
			console.log(interval)
		}
		let choices
		let answers
		let choicesGroup
		if (config.intervalNames) {
			choices = config.intervalNames
			answers = [choices.indexOf(interval.name)]
		}else{
			let steps = config.steps.map((i) => {return chineseNumbers[i-1]+'度'})
			choices = [...config.types, ...steps]
			choicesGroup = [config.types.length]
			let step = chineseNumbers[interval.step]+'度'
			answers = [choices.indexOf(interval.type), choices.indexOf(step)]
		}

		var question = new Question()
		question.choices = choices
		question.answers = answers
		if (choicesGroup) {
			question.choicesGroup = choicesGroup
		}

		return {question: question, interval: interval}
	}
	static intervalQuestionIdentification(questions, config) {
		let {question, interval} = this.createRandomChooseQuestion(config)

		let score = {clef: config.clef, notes:[{note:8,elems:[interval.root, interval.head]}]}
		question.desc = '判断音程性质'
		question.score = score
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
	static intervalQuestion1(questions, config) {
		var interval = QInterval.randomInterval(config)
		var question = new Question()
		interval.root.fill='grey'
		interval.root.readonly=true
		question.desc = '以指定音为底音,构成音程：'+interval.name
		question.score = {clef: config.clef, chordOnly: true, notes: [{note:8,elems:[interval.root]}]}
		question.answers = {notes: [{note:8,elems:[interval.root, interval.head]}]}
		question.keyboard = KeyboardType.WriteNotes
		questions.push(question)
	}
	static intervalQuestion2(questions, config) {
		var interval = QInterval.randomInterval(config)
		var question = new Question()
		interval.head.fill='grey'
		interval.head.readonly=true
		question.desc = '以指定音为冠音,构成指定的音程：'+interval.name
		question.score = {clef: config.clef, chordOnly: true, notes:[{note:8, elems:[interval.head]}]}
		question.answers = {notes: [{note:8, elems: [interval.root, interval.head]}]}
		question.keyboard = KeyboardType.WriteNotes
		questions.push(question)
	}
	static intervalQuestionKeyboard(questions, config) {
		var interval = QInterval.randomInterval(config)
		var question = new Question()
		question.desc = '弹奏指定低音的'+interval.name+'音程'
		question.score = {clef: config.clef, notes:[{note:1, elems:[interval.root]}]}
		question.keyboard = KeyboardType.PlayKeys
		question.answers = {ignoreOctave: true, notes: [{note:1, elems: [interval.root, interval.head]}]}
		questions.push(question)
	}
	static intervalQuestionEarTraining(questions, config) {
		let {question, interval} = this.createRandomChooseQuestion(config)
		question.desc = '选择弹奏的音程'
		question.midi = {events:[{tick: 0, note:interval.root.value, duration: 480}, {tick: 0, note: interval.head.value, duration: 480}]}
		if (config.harmony.includes('和声音程')) {
			question.midi.harmony = true
		}
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
}

export default QIntervalQuestions;



