import QNoteElem from './QNote';

export const KeyboardType = {
	ChoiceABCD 	: 1, //根据choices给出选项ABCD...
	Choice 		: 2, //根据choices给出选项
	WriteRhythm : 3, //写节奏
	PlayRhythm 	: 4, //弹奏节奏，只需要一个A4键
	PlayKeys 	: 5, //弹奏4个8度的按键
	WriteNotes 	: 6, //写作单音、音程、和弦、音阶
}

/*
question:{
	desc: '',
	score: {fifths:int, notes: []},
	choices: [string]
	answer: {notes: []} or index of choices
}
*/
export class Question {
	compareFreeChord(correctNote, inputValues) {
		//最低音要正确
		let firstCorrectValue = correctNote.elems[0].value		
		if (this.answers.ignoreOctave) {
			if (inputValues[0]%12 !== firstCorrectValue%12) {
				return false
			}
		}else if (inputValues[0] !== firstCorrectValue) {
			return false
		}
		//自由模式，只要最低音正确，其他音可以多，顺序可以颠倒.
		for (let elem of correctNote.elems) {
			if (inputValues.findIndex((v) => {
				if (this.answers.ignoreOctave) {
					return (v%12 === elem.value%12)
				}else{
					return v === elem.value
				}
			})<0) {
				return false
			}
		}
		for (let v of inputValues) {
			if (correctNote.elems.findIndex((elem) => {
				if (this.answers.ignoreOctave) {
					return (v%12 === elem.value%12)
				}else{
					return v === elem.value
				}
			})<0) {
				return false
			}
		}
		return true
	}
	compareNote(correctNote, inputValues) {
		if (inputValues.length>1) {
			inputValues.sort()
		}
		if (this.answers.freeChord) {
			return this.compareFreeChord(correctNote, inputValues)
		}
		//标准评测，所有音的个数、音值、顺序都必须正确
		if (inputValues.length !== correctNote.elems.length) {
			return false
		}
		for (let i = 0; i < correctNote.elems.length; i++) {
			let elem = correctNote.elems[i]
			let v = inputValues[i]
			if (this.answers.ignoreOctave) {
				if (v%12 !== elem.value%12) {
					return false
				}
			}else{
				if (v !== elem.value) {
					return false
				}
			}
		}
		return true
	}
	/*
	params: 
		answers: [Int] 选项编号的数组
		notes: [Int] 按键键值的数组
	*/
	checkAnswer(answers, notes) {
		if (this.choices) {
			if ((this.canChooseOne && answers.length>0) || this.answers.length === answers.length) {
				for (let i = 0; i < answers.length; i++) {
					if(!this.answers.includes(answers[i])) {
						return false
					}
				}
				return true
			}
		}else if (this.keyboard === KeyboardType.PlayKeys) {
			console.log('checkAnswer:')
			console.log(this.answers.notes)
			console.log(notes)
			if (this.answers.notes.length > 0 && notes && notes.length > 0) {
				if (this.answers.notes.length === 1) { //单音、和弦、音程的弹奏题
					let correctNote = this.answers.notes[0]
					let inputValues
					if (notes[0].elems) {
						inputValues = notes[0].elems.map((elem) => {
							return elem.value
						})
					}else{
						inputValues = notes
					}
					return this.compareNote(correctNote, inputValues)
				}else{ //音阶
					if (notes[notes.length-1]<=notes[0]) {
						return false
					}
					for (let i = 0; i < this.answers.notes.length; i++) {
						let correctNote = this.answers.notes[i]
						let inputValue = notes[i]
						if (correctNote.elems[0].value%12 !== inputValue%12) {
							return false
						}
					}
					return true
				}
			}
		}else if (this.answers.notes) {
			console.log('checkAnswer:')
			console.log(this.answers.notes)
			console.log(notes)
			if (this.answers.notes.length === notes.length && notes.length > 0) {
				for (let n in notes) {
					let note = notes[n]
					if (this.writeRhythm) {
						let correctNote = this.answers.notes[n]
						if (note.note !== correctNote.note) {
							return false
						}
						continue
					}
					for (let elem of note.elems) {
						if (!elem.alter) {
							elem.alter = 0
						}
						let answerNote = QNoteElem.noteFromLine(elem.line, elem.alter, this.score.clef ? this.score.clef : 'treble') 
						console.log(answerNote)
						if (this.answers.notes[n].elems.findIndex(function(correctNote, index){
							console.log('answer:'+answerNote.value+' correct:'+correctNote.value)
							if (this.answers.ignoreOctave) {
								return (answerNote.step === correctNote.step && answerNote.alter === correctNote.alter)
							}else{
								return (answerNote.step === correctNote.step && answerNote.value === correctNote.value)
							}
						}, this)<0) {
							return false
						}
					}
				}
				return true
			}
		}
		return false
	}
	answerNumber() {
		if (this.answers.notes) {
			let len = 0
			for (let note of this.answers.notes) {
				len += note.elems.length
			}
			return len
		}else{
			return this.answers.length
		}
	}
}

class BaseQuestions {
	static randomClef(config) {
		let clef = 'treble' //(config && config.clefs.includes('低音')) ? 'bass' : 'treble'
		if (config && config.clefs) {
			let clefType = config.clefs[QNoteElem.random(config.clefs.length-1)]
			if (clefType==='低音') {
				clef = 'bass'
			}else if (clefType==='中音') {
				let types = ['alto', 'tenor', 'soprano', 'mezzosoprano', 'baritone']
				clef = types[QNoteElem.random(types.length-1)]
			}
		}
		return clef
	}
	static eventsOfNotes(notes, beats=4, beatUnit=4) {
		let events = []
		let tick = 0
		let measureDuration = beats*480*4/beatUnit
		// if (window.device!=='piano') {
		// 	let countdown = 4
		// 	let duration = 480
		// 	if (beatUnit===8) {
		// 		countdown=6
		// 		duration=240
		// 	}
		// 	for (let i = 0; i < countdown; i++) {
		// 		events.push({tick: tick, note: 78, duration: duration, vel: i===0 ? 100 : 40})
		// 		tick += duration
		// 	}
		// }
		let tupletStartIndex = 0
		let tupletCount = 0
		for (let i = 0; i < notes.length; i++) {
			let note = notes[i]
			let duration = 480*4/note.note
			if (note.dots) {
				duration *= 1.5
			}
			if (note.tuplet && note.tuplet.count) {
				tupletCount = note.tuplet.count
				tupletStartIndex = i
			}
			if (tupletCount>0 && i<tupletStartIndex+tupletCount) {
				if (tupletCount===2 || tupletCount===4) {
					duration *= 3
				}else if (tupletCount===3) {
					duration *= 2
				}
				duration /= tupletCount
			}
			if (!note.rest) {
				let vel = 60
				if (tick%measureDuration===0) {
					vel = 90
				}
				events.push({tick: tick, note: note.elems[0].value, duration: duration-16, vel: vel})
			}
			tick += duration
		}
		return events
	}
}
export default BaseQuestions;



